import makeStyles from '@material-ui/core/styles/makeStyles';

const drawerWidth = 240;

const styles = makeStyles((theme) => ({
    root: {
      display: 'flex',
      outline:'none'
    },
    toolbar: {
      paddingRight: 24, // keep right padding when drawer closed
    },
    toolbarIcon: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'flex-end',
      padding: '0 8px',
      ...theme.mixins.toolbar,
    },
    appBar: {
      zIndex: theme.zIndex.drawer + 1,
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      background: "#fff"
    },
    appBarShift: {
      marginLeft: drawerWidth,
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    menuButton: {
      marginRight: 36,
    },
    menuButtonHidden: {
      display: 'none',
    },
    title: {
      flexGrow: 1,
    },
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
    },
    drawerContainer: {
      overflow: 'auto',
    },
    drawerPaper: {
      position: 'relative',
      whiteSpace: 'nowrap',
      width: drawerWidth,
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    drawerPaperClose: {
      overflowX: 'hidden',
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      width: theme.spacing(7),
      [theme.breakpoints.up('sm')]: {
        width: theme.spacing(9),
      },
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
      flexGrow: 1,
      height: 'auto',
      overflow: 'auto',
    },
    paper: {
      padding: theme.spacing(2),
      display: 'flex',
      overflow: 'auto',
      flexDirection: 'column',
    },
    fixedHeight: {
      height: 240,
    },
    button: {
      position: 'fixed',
      right: 15,
      top: 15,
      height: 'auto',
      color: "white",
      background: "#3dd19d",
    },
  
    logoutButton: {
      position: 'fixed',
      right: 40,
      top: 25,
      height: '30px',
      color: "black",
      background: "white",
      fontSize: '18px'
  
    },
    allMyTasksButton: {
      height: '30px',
      color: "black",
      background: "white",
      fontSize: '14px'
    },
  
    todoListRoot: {
      width: '100%',
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
      
    },
    emptyListText: {
      display: 'block',
      whiteSpace: 'initial',
      padding: 20,
      textAlign: 'center',
      fontSize: 14,
      color: '#777777'
    },
    nested: {
      paddingLeft: theme.spacing(5),
    },
    addTodoListButton: {
      outline: '0 !important',
      width: '100%'
    },
    listRemoveButton: {
      outline: '0 !important',
    },
  }));

  export default styles;