import React from 'react';

import {
    LinkedIn as LinkedInIcon,
    GitHub as GithubIcon,
    LinkRounded,
    
  } from "@material-ui/icons";

import BootstrapTooltip from "../../components/BootstrapTooltip";

const LinksSection = () => {
    return (
        <span className="w-full space-x-3 row-center">
            <BootstrapTooltip placement="top" title="coming soon">
                <span><GithubIcon className="w-10 h-10 text-green" /></span>
            </BootstrapTooltip>
            <BootstrapTooltip placement="top" title="coming soon">
                <span><LinkedInIcon className="w-10 h-10 text-green" /></span>
            </BootstrapTooltip>
            <BootstrapTooltip placement="top" title="coming soon">
                <span><LinkRounded className="w-10 h-10 text-green" /></span>
            </BootstrapTooltip>
        </span>
    )
}

export default LinksSection
