import React from 'react';
import {
    Zoom,
    Button,
    Typography
} from '@material-ui/core';

const FilterButton = ({ name, state, stat, onClick }) => {
    return (
        <>
            <Zoom timeout={500} in={true}>
              <Button
                color="black"
                outline="none"
                className="no-outline"
                style={
                  state
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
                onClick={onClick}
              >
                <Typography
                  style={state ? { fontWeight: "bold" } : null}
                >
                  {name}
                </Typography>{" "}
                <Typography variant="h6">
                  <Typography
                    style={{ paddingLeft: "5px" }}
                    display="inline"
                    variant="h6"
                    color="primary"
                  >
                    {stat}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
        </>
    )
}

export default FilterButton
