import React from 'react'

const TopStats = ( props ) => {
    const {
        numOfAssignedToMyselfCompleted,
        assigneeCompletedTasks,
        remainingAssigneeTasks,
        numOfAssignedToMyselfRemaining,
        numOfBoardsCompleted
    } = props;
    return (
        <span className="space-y-8 col-start">
            <span className="space-y-2 col-start">
            <span className="text-xs font-medium text-gray-400">COMPLETED TASKS</span>
            <span className="h-8 text-2xl font-bold text-center rounded-full w-28 bg-opacity-80 bg-green-lightest text-green">
                {numOfAssignedToMyselfCompleted+assigneeCompletedTasks}
            </span>
            </span>
            <span className="space-y-2 col-start">
            <span className="text-xs font-medium text-gray-400">INCOMPLETE TASKS ACROSS ALL BOARDS</span>
            <span className={`h-8 text-2xl font-bold text-center ${remainingAssigneeTasks+numOfAssignedToMyselfRemaining===0 ? "text-green bg-green-lightest" : "text-red-400 bg-red-100"} rounded-full w-28 bg-opacity-80`}>
                {remainingAssigneeTasks + numOfAssignedToMyselfRemaining}
            </span>
            </span>
            <span className="space-y-2 col-start">
            <span className="text-xs font-medium text-gray-400">BOARDS COMPLETED (ALL TIME)</span>
            <span className="h-8 text-2xl font-bold text-center rounded-full w-28 bg-opacity-80 bg-green-lightest text-green">
                {numOfBoardsCompleted}
            </span>
            </span>
        </span>
    )
}

export default TopStats
