export const filterArrOfObj = (arr, param)=>{
    const filteredArr = arr.reduce((acc, current) => {
      const x = acc.find(item => item[param] === current[param]);
      if (!x) {
        return acc.concat([current]);
      } else {
        return acc;
      }
    }, []);
  
    return filteredArr;
  }

export const dateMaker = (timestamp) => {
    // 'yyyy-mm-dd'
    const date = new Date(timestamp);
    const fullYear = date.getFullYear();
    const month = date.getMonth()+1;
    const day = date.getDate();
  
    return `${fullYear}-${month}-${day}`;
  }

export const dateMakerMonthly = (timestamp) => {
    // 'yyyy-mm'
    const date = new Date(timestamp);
    const fullYear = date.getFullYear();
    const month = date.getMonth()+1;
  
    return `${fullYear}-${month}`;
  }

const getWeekOfMonth = (dateObj) => {
    let d = new Date(dateObj);
    let date = d.getDate();
    let day = d.getDay();
  
    let weekOfMonth = Math.ceil((date - 1 - day) / 7);
  
    return weekOfMonth;
  }

export const dateMakerWeekly = (timestamp) => {
    // 'yyyy-mm-wom'
    const date = new Date(timestamp);
    const fullYear = date.getFullYear();
    const month = date.getMonth()+1;
    const wom = getWeekOfMonth(timestamp)+1;
  
    return `${fullYear}-${month}-${wom}`; 
}
