import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';

// import Snackbar from '@material-ui/core/Snackbar';
// import Alert from "@material-ui/lab/Alert";

import BootstrapTooltip from './BootstrapTooltip';
import demo_board from '../assets/images/demo_board.png';

import SuccessModal from './SuccessModal';
import useStyles from './BoardCardStyles';


const BoardCard = ({ boardObj, handleConfirmationOpen, setListToDelete, boardOnClick, currentUserId, selectedList }) => {
  const classes = useStyles();
  const [completedPercent, setCompletedPercent] = useState(0);

//   const handleExpandClick = () => {
//     setExpanded(!expanded);
//   };

  // console.log(boardObj);

  useEffect(() => {
      let numOfTasksCompleted = 0;
      let totalTasks = boardObj.todoList.length;
      if(boardObj.todoList.length>0){
        boardObj.todoList.map(taskObj=>{
            if(taskObj.isComplete) numOfTasksCompleted= numOfTasksCompleted + 1;
        })
      }
      setCompletedPercent(totalTasks && numOfTasksCompleted ? Math.round(( numOfTasksCompleted * 100 ) / totalTasks) : 0);
      return () => {
      }
  }, [])

  const createdBySelf = boardObj.createdBy===currentUserId;

  const handleShareBtnClick = (boardId) => {
    const loc = window.location;
    navigator.clipboard.writeText(`${loc.origin}${loc.pathname}?id=${boardId}`);
    
    setSeverity('success');
    setAlertMessage("Board's link has been copied to the clipboard.");
    setAlertOpen(true);
  }

  // SUCESS ALERT
  const [alertOpen, setAlertOpen] = useState(false);
  const [severity, setSeverity] = useState('success');
  const [alertMessage, setAlertMessage] = useState('');
  const handleCloseAlert = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setAlertOpen(false);
  };
  // 

  return (
    <Card elevation={3} className={classes.root}>
      <CardHeader
        // avatar={
        //   <Avatar aria-label="recipe" className={classes.avatar}>
        //     R
        //   </Avatar>
        // }
        // action={
        //   <IconButton aria-label="settings">
        //     <MoreVertIcon />
        //   </IconButton>
        // }
        onClick={boardOnClick}
        className="text-green pointer"
        title={ boardObj.name }
        subheader={ boardObj.createdByUsername ? boardObj.createdByUsername : boardObj.createdBy }
      />
      <CardMedia
        onClick={boardOnClick}
        style={{height:'180px', width:'100%'}}
        className={`${classes.media} cursor-pointer`}
        image= {demo_board} //"/static/images/cards/paella.jpg"
        title="Featured Image"
      />
      <CardContent className="h-14"> 
        {/* <Typography variant="h5" color="textSecondary" component="h4">
            Board Name
        </Typography> */}
        <Typography className="h-10" variant="body2" color="textSecondary" component="p">
          {
            boardObj.description ? boardObj.description : 'This is a board description explaining everything this board will do.' 
          }
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <span className="flex justify-between w-6/12">
        <BootstrapTooltip placement="top" title="Coming Soon">
          <span className="w-8 h-8 pointer">
            <IconButton disabled className="w-8 h-8" aria-label="add to favorites">
              <FavoriteIcon className="text-red-700" />
            </IconButton>
          </span>
        </BootstrapTooltip>
        <BootstrapTooltip placement="top" title="Coming Soon">
          <span className="w-8 h-8 pointer">
            <IconButton disabled className="w-8 h-8" aria-label="edit">
              <EditIcon />
            </IconButton>
          </span>
        </BootstrapTooltip>
        <BootstrapTooltip arrow placement="top" title={boardObj.createdBy!==currentUserId ? "Boards created by others can't be deleted by you" : "Delete Board"}>
          <span className="w-8 h-8 pointer">
            <IconButton 
                disabled={boardObj.createdBy!==currentUserId}
                className="w-8 h-8 pointer"
                onClick={(e)=> {
                        e.stopPropagation();
                        setListToDelete(boardObj);
                        handleConfirmationOpen(true);
                      }} 
                aria-label="delete">
              <DeleteIcon />
            </IconButton>
          </span>
        </BootstrapTooltip>
        <BootstrapTooltip placement="top" title={!boardObj.isPublic ? "Board is not public" : "Share Board"}>
          <span className="w-8 h-8 pointer">
            <IconButton 
              onClick={()=>handleShareBtnClick(boardObj.id)} 
              disabled={boardObj.id===selectedList.id ? !selectedList.isPublic : !boardObj.isPublic} 
              className="w-8 h-8" aria-label="share"
              >
              <ShareIcon />
            </IconButton>
          </span>
        </BootstrapTooltip>
        </span>
        <span className="flex items-center justify-end w-6/12 pr-2 text-green-dark">
            <span className="mr-1 text-lg font-bold">{ completedPercent }%</span> Complete
        </span>
      </CardActions>

      <SuccessModal title={'Success'} subtitle={alertMessage} open={alertOpen} setOpen={setAlertOpen} />

    </Card>
  );
}

      {/* <Snackbar
        open={alertOpen}
        autoHideDuration={4000}
        onClose={handleCloseAlert}
        // anchorOrigin={{vertical: 'top', horizontal:'center'}}
      >
        <Alert variant="filled" onClose={handleCloseAlert} severity={severity}>
          {alertMessage}
        </Alert>
      </Snackbar> */}

export default BoardCard;