import makeStyles from '@material-ui/core/styles/makeStyles';

const useStyles = makeStyles((theme) => ({
    container: {
      paddingTop: theme.spacing(1),
      paddingBottom: theme.spacing(1),
    },
    root: {
      width: '100%',
      // maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
      marginTop: '10px',
      marginBottom: '10px',
      paddingTop: '0px',
      outline: 'none',
    },
  
    
    expensionSummary: {
      position: 'relative',
      display: 'block',   
      border: '0px',
    },
  
    expansionPanelSummaryContent: {
      marginTop: '0px',
      marginBottom: '0px',
      border: '0px',
      '&:hover':{
        transition: 'all 0.2s linear',
        background: '#f7f5f5',
      }
    },
    
    
    todoItemFormControl: {
      width: 'calc(100% - 180px)',
      height: '0 px',
      marginTop: '0px',
      marginBottom: '0px',
      paddingTop: '0px',
      paddingBottom: '0px',
      
    },
  
  
    optionButtonArea: {
      position: 'fixed',
      outline: '0 !important',
      padding: 0,
      outline: 'none',
  
      // marginTop: '6px',
    },
  
  
    optionButtons: {
      outline: '0 !important',
      margin: 'auto',
      
  
    },
    dateField: {
      position: 'relative',
      outline: '0 !important',
      margin: 'auto',
      textAlign: 'right',
      // marginRight: 15,
      marginTop: 0,
      border: '0px solid #ccc',
      padding: 0,
      borderRadius: 10,
      fontSize: '0.875rem',
    },
  
    
    
    rootButton: {
      display: 'flex',
      
    },
   
    optionButtonPopper: {
      zIndex: 9
    },
    
    listTitle: {
      marginRight: '10px',
      justifyContent: "right",
      padding: '6px',
      textAlign: 'left',
      width: '100%',
      fontSize: 25,
      borderBottom: 'solid 4px #3dd19d',
  
    },
    search: {
      fontSize: 17,
      marginTop: '10px',
      position: 'relative',
      borderRadius: 5,
      width: '50%',
      justifyContent: "right",
  
      
    },
    searchIcon: {
      padding: theme.spacing(0, 2),
      height: '100%',
      position: 'absolute',
      pointerEvents: 'none',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    inputRoot: {
      color: 'inherit',
    },
    inputInput: {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
      transition: theme.transitions.create('width'),
      width: '100%',
      [theme.breakpoints.up('md')]: {
        width: '20ch',
      }
    },
    addTodoItemButton: {
      display: 'block',
      outline: '0 !important',
      margin: 'auto',
      marginTop: 5
    },
    addTodoItemButtonIcon: {
      color: '#3dd19d',
      fontSize: 30
    },
  
    addTaskButton: {
      color: '#3dd19d',
      fontSize: 17,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      width: '120px',
      height: '100%',
      padding: '9px',
      marginTop: '10px',
    },
  
  
    taskProps:{
      padding: theme.spacing(0, 2),
      pointerEvents: 'none',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
  
    paper:{
      //background: '',
      marginTop: '15px',
      boxshadow: 'none',
      border: 'none',
    },
  
    filterOptions: {
      marginTop: '15px',
      alignItems: 'center',
      display: 'flex',
      justifyContent: 'center',
    },
  
    taskPropContainer:{
      justifyContent: 'center',
      border: 'solid 1black',
      alignItems: 'center',
    },
  
  }));

export default useStyles;  