import React, { useState, useEffect } from "react";

import Checkbox from "@material-ui/core/Checkbox";
import Container from "@material-ui/core/Container";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import IconButton from "@material-ui/core/IconButton";
import InputBase from "@material-ui/core/InputBase";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Snackbar from "@material-ui/core/Snackbar";
import Alert from "@material-ui/lab/Alert";

import {
  AddCircleOutline as AddCircleOutlineIcon,
  // AutorenewTwoTone,
  // ContactsOutlined,
  Delete as DeleteIcon,
  Edit as EditIcon,
  ExpandMore as ExpandMoreIcon,
  Search as SearchIcon,
  FileCopy as CopyIcon,
  Share,
} from "@material-ui/icons";

import TimeAgo from "react-timeago";
import Linkify from "react-linkify";

import BootstrapTooltip from "./BootstrapTooltip";
import SuccessAnimation from "./SuccessAnimation";
import useStyles from "./todoItemListStyles";

import "./style.css";

import calculateFullDate from "./utils/twoDigitDateTextMaker";
import { Fade, Switch, TextField, Zoom } from "@material-ui/core";

function TodoItemList(props) {
  // Snackbar ALERT
  const [alertOpen, setAlertOpen] = useState(false);
  const [severity, setSeverity] = useState("success");
  const [alertMessage, setAlertMessage] = useState("");
  const handleCloseAlert = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setAlertOpen(false);
  };

  const handleSnackbarOpen = (severity, message) => {
    setSeverity(severity);
    setAlertMessage(message);
    setAlertOpen(true);
  };
  //

  useEffect(() => {
    console.log("heeeeeeeeeeeadafafasssssssssssssss");
  }, [props.selectedList.length]);

  const mobileWidth = window.screen.availWidth < 700;

  const classes = useStyles();
  let lists = props.list.todoList;
  let filteredLists = lists;

  const [mainKey, setMainKey] = useState(123);

  const [openCompletedAlert, setOpenCompletedAlert] = useState(false);

  const [searchText, setSearchText] = useState("");
  const [sortCompleted, setSortCompleted] = useState(false);
  const [sortIncomplete, setSortIncomplete] = useState(false);

  const [filterDue, setFilterDue] = useState(false);
  const [filterOverdue, setFilterOverdue] = useState(false);

  const [sortValue, setSortValue] = useState("due");
  const [sortCreated, setSortCreated] = useState(false);
  const [sortDue, setSortDue] = useState(true);

  const [filterAssigned, setFilterAssigned] = useState(false);
  const [filterAssignee, setFilterAssignee] = useState(false);

  let remaining = [];
  if (props.list.id) {
    remaining = props.list.todoList.filter((list) => !list.isComplete);
  } else {
    remaining = [];
  }

  let notRemaining = [];
  if (props.list.id) {
    notRemaining = props.list.todoList.filter((list) => list.isComplete);
  } else {
    notRemaining = [];
  }

  let allTasks = [];
  if (props.list.id) {
    allTasks = props.list.todoList.filter((list) => list);
  } else {
    allTasks = [];
  }

  // SORT BY:   CREATE DATE / DUE DATE
  const handleChange = async (event) => {
    await setSortValue(event.target.value);
    // await console.log(sortValue);
    if (sortValue === "created") {
      await setSortCreated(false);
      await setSortDue(true);
    } else if (sortValue === "due") {
      await setSortCreated(true);
      await setSortDue(false);
    }
  };
  if (sortCreated && props.list.id && filteredLists.length > 0) {
    filteredLists = filteredLists.sort((a, b) => a.createDate - b.createDate);
  } else if (sortDue && props.list.id && filteredLists.length > 0) {
    filteredLists = filteredLists.sort((a, b) => a.date - b.date);
  }

  // // FILTER BY: COMPLETE / INCOMPLETE
  // if (props.list.id) {
  //   if(sortCompleted){
  //     filteredLists = props.list.todoList.filter((list)=> list.isComplete);
  //   }
  //   else if(sortIncomplete){
  //     filteredLists = props.list.todoList.filter((list)=> !list.isComplete);
  //   }
  //   else filteredLists = props.list.todoList.filter((list) => list.name.toLowerCase().includes(searchText.toLocaleLowerCase()));
  // } else {
  //   filteredLists = [];
  // }

  // this will randomly choose words from an array and return it to the SuccessAnimation component as text prop
  const chooseAlertText = () => {
    const sentences = [
      "You Rock!!",
      "Let's gooo!!",
      "Great Work!",
      "Done with that!",
      "Nice Work!",
      "Woohooo",
      "Well done!",
      "Great Job",
    ]; // add as many affirmations as you want
    const selectedText = sentences[Math.round(Math.random() * 3)]; // replace 3 with 1 less than the number of messages in the array above

    return selectedText;
  };

  // FILTER BY: DUE / OVERDUE & FILTER BY: COMPLETE / INCOMPLETE
  let dueRemaining = [];
  let overdueRemaining = [];
  let assigned = [];
  let assignee = [];

  const todayDate = new Date().getTime();

  if (props.list.id) {
    dueRemaining = remaining.filter((list) => new Date(list.date) > new Date());
    overdueRemaining = remaining.filter((list) => list.date < todayDate);

    assigned = props.list.todoList.filter(
      (list) =>
        list.assignedTo !== props.currentUserEmail &&
        list.assignedTo !== "" &&
        list.assignedBy === props.currentUserEmail
    );
    assignee = props.list.todoList.filter(
      (list) =>
        list.assignedBy !== props.currentUserEmail &&
        list.assignedTo === props.currentUserEmail
    );

    if (filterDue) {
      filteredLists = dueRemaining;
    } else if (filterOverdue) {
      filteredLists = overdueRemaining;
    } else if (sortCompleted) {
      filteredLists = props.list.todoList.filter((list) => list.isComplete);
    } else if (sortIncomplete) {
      filteredLists = props.list.todoList.filter((list) => !list.isComplete);
    } else if (filterAssigned) {
      filteredLists = assigned;
    } else if (filterAssignee) {
      filteredLists = assignee;
    } else
      filteredLists = props.list.todoList.filter(
        (list) =>
          list.name.toLowerCase().includes(searchText.toLocaleLowerCase()) ||
          list.detail.toLowerCase().includes(searchText.toLocaleLowerCase())
      );
  } else {
    filteredLists = [];
    const dueRemaining = [];
    const overDueRemaining = [];
  }

  const sortByAll = async () => {
    await setFilterAssigned(false);
    await setFilterAssignee(false);
    await setSortIncomplete(false);
    await setSortCompleted(false);
    await setFilterDue(false);
    await setFilterOverdue(false);
  };

  const editTodo = (event, list, index) => {
    event.stopPropagation();
    props.openEditDialog(list, index);
  };

  const removeTodo = (event, list, index) => {
    event.stopPropagation();
    const deletedTask = lists.splice(index, 1);
    const assignedToo = deletedTask[0].assignedTo;
    const assignedByy = deletedTask[0].assignedBy;
    // console.log('DELTED TASK:', deletedTask);
    // console.log('assigned by:', assignedByy, 'assigned To:', assignedToo);
    if (
      assignedToo !== props.currentUserEmail &&
      assignedToo !== "" &&
      assignedByy === props.currentUserEmail
    ) {
      // alert('helllllllllllllllllllllllllll');
      props.removeNewAssignment(
        props.selectedList.id,
        props.selectedList.name,
        deletedTask[0]
      );
      // console.log('selectrd Listttt',props.selectedList);
    }
    props.updateTodoList({ ...props.list, todoList: lists });
    setMainKey((mainKey) => mainKey + 1);
  };

  const checkboxToggle = async (event, list) => {
    if (event.target.tagName !== "INPUT") return;

    event.stopPropagation();

    list.isComplete = !list.isComplete;
    list.completedAt = new Date().getTime();

    // console.log(list.assignedTo, list.assignedBy);

    setMainKey((mainKey) => mainKey + 1); // for remounting the component (https://www.nikgraf.com/blog/using-reacts-key-attribute-to-remount-a-component)

    // open success message
    if (list.isComplete) {
      await setOpenCompletedAlert(true);
      setTimeout(async () => await setOpenCompletedAlert(false), 4000);
      // the function inside setTimeout will run after 4000 millisecond (written as second argument)
      // which means that animation message will be open for 4000ms (or 4 seconds)
    }

    // if(list.assignedTo!==props.currentUserEmail && list.assignedTo!==''){
    //   props.addNewAssignment(props.selectedList.id, props.selectedList.name, list);
    // }
    // if(list.assignedBy!==props.currentUserEmail && list.assignedTo!==''){
    //   props.addNewAssignment(props.selectedList.id, props.selectedList.name, list, "complex");
    // }
    if (list.assignedBy !== props.currentUserEmail && list.isComplete) {
      console.log("RAN CHECKKKK");
      const data = {
        name: props.list.name,
        todoList: lists,
        createdBy: props.list.createdBy,
        ...props.list,
      };
      props.updateTodoList(data, true, list.assignedTo, list.assignedBy);
    } else {
      props.updateTodoList({
        name: props.list.name,
        todoList: lists,
        createdBy: props.list.createdBy,
        ...props.list,
      });
    }
  };

  const componentDecorator = (href, text, key) => (
    <a
      className="text-blue-500"
      href={href}
      key={key}
      target="_blank"
      rel="noopener noreferrer"
    >
      {text}
    </a>
  );

  const duplicateTask = (event, list, index) => {
    props.setTaskToDuplicate(list);
    props.openNewTodoDialog(true);
  };

  const [isPublic, setIsPublic] = useState(
    props.list.isPublic ? props.list.isPublic : false
  );
  const handleIsPublicChange = (e) => {
    setIsPublic(e.target.checked);
    const tempList = { ...props.list, isPublic: e.target.checked };
    props.updateTodoList(tempList);
    props.setSelectedList(tempList);
    console.log("tempList", tempList);
  };

  useEffect(() => {
    setIsPublic(props.list.isPublic);
  }, [props.list.isPublic]);

  const loc = window.location;

  const handleBoardUrlCopy = (id) => {
    const url = `${loc.origin}${loc.pathname}?id=${id}`;
    navigator.clipboard.writeText(url);
    handleSnackbarOpen("success", "Board Url has been copied to the clipboard");
  };

  const animationTimeout = 500;

  if (props.list.id) {
    return (
      <Container key={mainKey} maxWidth="lg" className={classes.container}>
        <Grid container spacing={1}>
          <span className="w-auto mt-1 row-center">
            <Zoom timeout={animationTimeout} in={true}>
              <Typography aphy variant="h6" className={classes.listTitle}>
                Board: {props.list.name}
              </Typography>
            </Zoom>
            {isPublic && (
              <Zoom timeout={animationTimeout} in={isPublic}>
                <BootstrapTooltip
                  key={isPublic}
                  placement="top"
                  title={"Share Board"}
                >
                  <span className="w-8 h-8 pointer">
                    <IconButton
                      style={{ outline: "none" }}
                      color="primary"
                      onClick={() => handleBoardUrlCopy(props.list.id)}
                      className="w-8 h-8"
                      aria-label="share"
                    >
                      <Share />
                    </IconButton>
                  </span>
                </BootstrapTooltip>
              </Zoom>
            )}
          </span>
          {/* <span item xs={12} sm={12} md={4} lg={4}>
            <span className="h-full row-center">
              <div 
                className={`text-white big-btn bg-green ml-4 mt-2`}
                onClick={(event) => {props.openNewTodoDialog();}}
                >
                Add Task
            </div>
            </span>
          </span> */}
        </Grid>
        <Grid container spacing={1}>
          <Grid item height={50} xs={12} sm={12} lg={12}>
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                onClick={sortByAll}
                className="no-outline"
                style={
                  !sortIncomplete &&
                  !sortCompleted &&
                  !filterAssigned &&
                  !filterAssignee &&
                  !filterOverdue
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
              >
                <Typography
                  style={
                    !sortIncomplete &&
                    !sortCompleted &&
                    !filterAssigned &&
                    !filterAssignee &&
                    !filterOverdue
                      ? { fontWeight: "bold" }
                      : null
                  }
                >
                  All
                </Typography>
                <Typography variant="h6">
                  <Typography
                    style={{ paddingLeft: "15px" }}
                    display="inline"
                    variant="h6"
                    color="primary"
                  >
                    {" "}
                    {allTasks.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                outline="none"
                className="no-outline"
                style={
                  sortIncomplete
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
                onClick={async () => {
                  // compensate
                  await setFilterAssigned(false);
                  await setFilterAssignee(false);

                  await setSortIncomplete(true);
                  await setSortCompleted(false);
                  await setFilterDue(false);
                  await setFilterOverdue(false);
                }}
              >
                <Typography
                  style={sortIncomplete ? { fontWeight: "bold" } : null}
                >
                  Todue
                </Typography>{" "}
                <Typography variant="h6">
                  <Typography
                    style={{ paddingLeft: "5px" }}
                    display="inline"
                    variant="h6"
                    color="primary"
                  >
                    {" "}
                    {remaining.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                className="no-outline"
                style={
                  sortCompleted
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
                onClick={async () => {
                  // compensate
                  await setFilterAssigned(false);
                  await setFilterAssignee(false);

                  await setSortCompleted(true);
                  await setSortIncomplete(false);
                  await setFilterDue(false);
                  await setFilterOverdue(false);
                }}
              >
                <Typography
                  style={sortCompleted ? { fontWeight: "bold" } : null}
                >
                  Todone
                </Typography>{" "}
                <Typography variant="h6">
                  <Typography
                    style={{ paddingLeft: "5px" }}
                    display=""
                    variant="h6"
                    color="primary"
                  >
                    {" "}
                    {notRemaining.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                className="no-outline"
                style={
                  filterAssigned
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
                onClick={async () => {
                  await setFilterAssigned(true);
                  await setFilterAssignee(false);
                  await setSortIncomplete(false);
                  await setSortCompleted(false);
                  await setFilterDue(false);
                  await setFilterOverdue(false);
                }}
              >
                <Typography
                  style={filterAssigned ? { fontWeight: "bold" } : null}
                >
                  Assigned to others
                </Typography>{" "}
                <Typography variant="h6">
                  <Typography
                    style={{ paddingLeft: "5px" }}
                    display="inline"
                    variant="h6"
                    color="primary"
                  >
                    {" "}
                    {assigned.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                className="no-outline"
                style={
                  filterAssignee
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
                onClick={async () => {
                  await setFilterAssigned(false);
                  await setFilterAssignee(true);
                  await setFilterDue(false);
                  await setFilterOverdue(false);
                  await setSortCompleted(false);
                  await setSortIncomplete(false);
                }}
              >
                <Typography
                  style={filterAssignee ? { fontWeight: "bold" } : null}
                >
                  Assigned to me
                </Typography>{" "}
                <Typography variant="h6">
                  <Typography
                    style={{ paddingLeft: "5px" }}
                    display=""
                    variant="h6"
                    color="primary"
                  >
                    {" "}
                    {assignee.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                className="no-outline"
                style={
                  filterOverdue
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
                onClick={async () => {
                  // compensation
                  await setFilterAssigned(false);
                  await setFilterAssignee(false);

                  await setFilterDue(false);
                  await setFilterOverdue(true);
                  await setSortIncomplete(false);
                  await setSortCompleted(false);
                }}
              >
                <Typography
                  style={filterOverdue ? { fontWeight: "bold" } : null}
                >
                  Overdue
                </Typography>{" "}
                <Typography variant="h6">
                  <Typography
                    style={
                      overdueRemaining.length > 0
                        ? { paddingLeft: "5px", color: "red" }
                        : { paddingLeft: "5px", color: "#3dd19d" }
                    }
                    display=""
                    variant="h6"
                  >
                    {" "}
                    {overdueRemaining.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <Zoom timeout={animationTimeout} in={true}>
              <FormControl
                style={{ marginTop: "3px", marginLeft: "10px" }}
                component="fieldset"
              >
                {/* <FormLabel component="legend">Sort By:</FormLabel> */}
                <RadioGroup
                  style={{ display: "flex", flexDirection: "row" }}
                  aria-label="sort by"
                  name="sortCreateDue"
                  value={sortValue}
                  onChange={handleChange}
                >
                  <FormControlLabel
                    style={{ fontSize: "0.9rem" }}
                    value="created"
                    control={<Radio />}
                    label="Date Created"
                  />
                  <FormControlLabel
                    style={{ fontSize: "0.9rem" }}
                    value="due"
                    control={<Radio />}
                    label="Due Date"
                  />
                </RadioGroup>
              </FormControl>
            </Zoom>
            <div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon style={{ color: "#3dd19d" }} />
              </div>
              <InputBase
                style={{ marginTop: "10px" }}
                placeholder="Search your tasks…"
                onChange={(text) => {
                  setSearchText(text.target.value);
                }}
                value={searchText}
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
                inputProps={{ "aria-label": "search" }}
              />
              <span>
                {props.isLogged &&
                  props.list.createdBy === props.currentUserEmail && (
                    <span key={isPublic}>
                      <Fade timeout={animationTimeout} in={true}>
                        <span>
                          {isPublic ? "Board is Public" : "Board is Private"}
                        </span>
                      </Fade>
                      <Switch
                        checked={isPublic}
                        onChange={(e) => handleIsPublicChange(e)}
                        name="isPublic"
                        color="primary"
                        inputProps={{ "aria-label": "Is Board Public" }}
                      />
                    </span>
                  )}
              </span>
            </div>

            {props.isLogged && isPublic && (
              <Fade timeout={animationTimeout} in={props.isLogged && isPublic}>
                <span key={isPublic}>
                  <TextField
                    style={{ width: "350px" }}
                    onClick={() => handleBoardUrlCopy(props.list.id)}
                    label="Board Link"
                    margin="dense"
                    value={`${loc.origin}${loc.pathname}?id=${props.list?.id}`}
                    variant="outlined"
                  />
                </span>
              </Fade>
            )}
          </Grid>
          <Grid item xs={12} sm={3} lg={4}>
            <Paper className={classes.filterOptions}>
              {/* <FormControl component="fieldset"> */}
              {/* <FormLabel component="legend">Sort By:</FormLabel> */}
              {/* <RadioGroup style={{display:'flex', flexDirection:'row'}} aria-label="sort by" name="sortCreateDue" value={sortValue} onChange={handleChange}>
                  <FormControlLabel  value='allFilter' control={<Radio />} label="All" />
                  <FormControlLabel  value='allToDue' control={<Radio />} label="ToDue" />
                  <FormControlLabel style={{fontSize:'0.9rem'}} value='allOverDue' control={<Radio />} label="OverDue" />
                </RadioGroup>
              </FormControl> */}
            </Paper>
          </Grid>
          <Grid item xs={12} sm={3}>
            <Paper className={classes.filterOptions}>
              {/* <Button color="black" onClick={sortByAll}>
                <Typography style={!sortIncomplete && !sortCompleted?{fontWeight:'bold'} : null}>All</Typography> <Typography variant="h6"><Typography style={{paddingLeft: '15px'}} display="inline" variant="h6" color="primary"> {allTasks.length}</Typography></Typography>
              </Button> */}

              {/* <Button color="black" onClick={async () => {
                await setSortCreated(true);
                await  setSortDue(false);
                }}>
                <Typography style={sortCreated?{fontWeight:'bold'} : null}>Date Created</Typography>
              </Button>
              <Button color="black" onClick={async () => {
                await setSortCreated(false);
                await  setSortDue(true);
                }}>
        
                <Typography style={sortDue?{fontWeight:'bold'} : null}>Due Date</Typography>

              </Button>  */}
            </Paper>
          </Grid>
          <Grid item xs={12} sm={3}></Grid>
        </Grid>

        {/* <div className={classes.filterAread}>
          
        </div> */}
        <Zoom timeout={animationTimeout} in={true}>
          <BootstrapTooltip placement="bottom" title="Ctrl+Enter">
            <span className="h-full row-center">
              <div
                className={`text-white big-btn bg-green mb-2`}
                onClick={(event) => {
                  props.openNewTodoDialog();
                }}
              >
                Add Task
              </div>
            </span>
          </BootstrapTooltip>
        </Zoom>
        {props.list.todoList.length > 0 && (
          <div className={classes.root}>
            {filteredLists.map((list, index) => {
              const fullDate = calculateFullDate(list.date);
              return (
                <ExpansionPanel
                  // expanded={}
                  key={list.taskId}
                >
                  <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />} // !mobileWidth &&
                    aria-label="Expand"
                    aria-controls="additional-actions1-content"
                    id="additional-actions1-header"
                    classes={{
                      content: classes.expansionPanelSummaryContent,
                    }}
                  >
                    {mobileWidth ? (
                      <span className="flex flex-col justify-center w-full">
                        <FormControlLabel
                          style={{ pointerEvents: "none" }}
                          className={`${classes.todoItemFormControl} py-3 px-1`}
                          aria-label="Acknowledge"
                          onFocus={(event) => event.stopPropagation()}
                          control={
                            <Checkbox
                              style={{ pointerEvents: "auto" }}
                              onClick={(event) => {
                                checkboxToggle(event, list);
                              }}
                              checked={list.isComplete}
                              disabled={
                                list.assignedBy === props.currentUserEmail ||
                                list.assignedTo === props.currentUserEmail
                                  ? false
                                  : true
                              }
                            />
                          }
                          label={list.name}
                          style={{ width: "100%", color: "black" }}
                        />

                        {/* <span className="space-x-1 row-center">
                    {
                      props.isLogged && list.assignedTo===props.currentUserEmail && list.assignedBy!==props.currentUserEmail ?
                      <Typography className="pb-1 pl-1 col-center" variant="subtitle2">
                        <span>Assigned By:</span>
                        <span>{list.assignedBy}</span>
                      </Typography>
                      : props.isLogged && list.assignedTo && list.assignedBy===props.currentUserEmail ?
                        <Typography className="pb-1 pl-1 col-center" variant="subtitle2">
                          <span>Assigned To:</span> 
                          <span>{list.assignedTo}</span>
                        </Typography>
                        : null
                    }
                    <BootstrapTooltip placement="top" title="Sum of urgency, impact and difficulty">
                      <span className="ml-1 min-w-max col-center">
                        <span className="px-2 py-1 min-w-min rounded-2xl bg-green-lightest">
                          {
                            1 + (typeof(list.level)==='number' ? list.level : 1) 
                            + (typeof(list.urgent)==='number' ? list.urgent : 1)
                            + (typeof(list.important)==='number' ? list.important : 1)
                          }
                        </span>
                      </span>
                    </BootstrapTooltip>
                  </span> */}
                        <span className="row-center">
                          {/* <Box component="span" className="flex flex-wrap w-24" pl={2} pt={2}>
                      <Typography variant="subtitle2" 
                        style={
                          (list.date<todayDate && list.isComplete) 
                          ? {color: "black"} 
                          : (list.date<todayDate && !list.isComplete) ? {color: "red"} : {color:'green'}}
                      >
                        <TimeAgo
                          live={false} // use false to increase performance (live means it updates est. time automatically without reloading the page)
                          date={list.date}  
                        />
                      </Typography>
                    </Box> */}
                          {/* <span className="flex"> */}
                            {/* <Typography className={classes.dateField}>
                              {fullDate.date}
                              <br />
                              {fullDate.clock}
                            </Typography> */}

                            {/* <>
                        <BootstrapTooltip placement="top" title={list.assignedBy===props.currentUserEmail ? "Edit Task" : "Tasks added by others can't be edited by you"}>
                            <span className="pointer">
                              <IconButton aria-label="edit" disabled={list.assignedBy===props.currentUserEmail? false : true} className={classes.optionButtons} onClick={(event) => {
                                editTodo(event, list, index);
                              }}>
                                  <EditIcon/>
                              </IconButton>
                            </span>
                        </BootstrapTooltip>
                        <BootstrapTooltip placement="top" title="Duplicate Task">
                          <span className="pointer">
                            <IconButton aria-label="duplicate" className={classes.optionButtons} onClick={(event) => {
                              duplicateTask(event, list, index);
                            }}>
                              <CopyIcon />
                            </IconButton>
                          </span>
                        </BootstrapTooltip>
                        <BootstrapTooltip placement="top" title={list.assignedBy===props.currentUserEmail ? "Delete Task" : "Tasks added by others can't be deleted by you"}>
                            <span className="pointer">
                              <IconButton aria-label="delete" disabled={list.assignedBy===props.currentUserEmail? false : true} className={classes.optionButtons} onClick={(event) => {
                              removeTodo(event, list, index);
                              }}>
                                <DeleteIcon/>
                              </IconButton>
                            </span>
                        </BootstrapTooltip>
                      </> */}
                          {/* </span> */}
                        </span>
                      </span>
                    ) : (
                      <>
                        {
                          <Fade in={true}>
                            <FormControlLabel
                              style={{
                                pointerEvents: "none",
                                transitionDelay: "800ms",
                              }}
                              className={classes.todoItemFormControl}
                              aria-label="Acknowledge"
                              onFocus={(event) => event.stopPropagation()}
                              control={
                                <Checkbox
                                  style={{ pointerEvents: "auto" }}
                                  onClick={(event) => {
                                    checkboxToggle(event, list);
                                  }}
                                  checked={list.isComplete}
                                  disabled={
                                    list.assignedBy ===
                                      props.currentUserEmail ||
                                    list.assignedTo === props.currentUserEmail
                                      ? false
                                      : true
                                  }
                                />
                              }
                              label={list.name}
                            ></FormControlLabel>
                          </Fade>
                        }

                        <BootstrapTooltip
                          placement="top"
                          title="Sum of urgency, impact and difficulty"
                        >
                          <span className="ml-1 mr-3 space-x-1 min-w-max row-center">
                            {/* <span className="">Todue Points</span> */}
                            <span className="text-green-dark min-w-min">
                              {1 +
                                (typeof list.level === "number"
                                  ? list.level
                                  : 1) +
                                (typeof list.urgent === "number"
                                  ? list.urgent
                                  : 1) +
                                (typeof list.important === "number"
                                  ? list.important
                                  : 1)}
                            </span>
                            <span>points</span>
                          </span>
                        </BootstrapTooltip>

                        {props.isLogged &&
                        list.assignedTo &&
                        list.assignedBy !== props.currentUserEmail ? (
                          <Typography variant="subtitle2">
                            Assigned By:
                            <BootstrapTooltip
                              placement="top"
                              title="View Profile"
                            >
                              <button
                                className="text-blue-500 no-outline hover:text-blue-700"
                                onClick={() => {
                                  props.setOpenProfile(true);
                                  props.setProfileUserId(list.assignedBy);
                                }}
                              >
                                {list.assignedBy}
                              </button>
                            </BootstrapTooltip>
                          </Typography>
                        ) : props.isLogged &&
                          list.assignedTo &&
                          list.assignedBy === props.currentUserEmail ? (
                          <Typography variant="subtitle2">
                            Assigned To:
                            <BootstrapTooltip
                              placement="top"
                              title="View Profile"
                            >
                              <button
                                className="text-blue-500 no-outline hover:text-blue-700"
                                onClick={() => {
                                  props.setOpenProfile(true);
                                  props.setProfileUserId(list.assignedTo);
                                }}
                              >
                                {list.assignedTo}
                              </button>
                            </BootstrapTooltip>
                          </Typography>
                        ) : null}

                        <Box component="span" width="200px" pl={2} pt={2}>
                          <Typography
                            variant="subtitle2"
                            style={
                              list.date < todayDate && list.isComplete
                                ? { color: "black" }
                                : list.date < todayDate && !list.isComplete
                                ? { color: "red" }
                                : { color: "green" }
                            }
                          >
                            <TimeAgo
                              live={false} // use false to increase performance (live means it updates est. time automatically without reloading the page)
                              date={list.date}
                            />
                          </Typography>
                        </Box>

                        <Typography className={`${classes.dateField} mr-4`}>
                          {fullDate.date} {fullDate.clock}
                        </Typography>

                        <>
                          <BootstrapTooltip
                            placement="top"
                            title={
                              list.assignedBy === props.currentUserEmail
                                ? "Edit Task"
                                : "Tasks added by others can't be edited by you"
                            }
                          >
                            <span className="pointer">
                              <IconButton
                                aria-label="edit"
                                disabled={
                                  list.assignedBy === props.currentUserEmail
                                    ? false
                                    : true
                                }
                                className={classes.optionButtons}
                                onClick={(event) => {
                                  editTodo(event, list, index);
                                }}
                              >
                                <EditIcon />
                              </IconButton>
                            </span>
                          </BootstrapTooltip>
                          <BootstrapTooltip
                            placement="top"
                            title="Duplicate Task"
                          >
                            <span className="pointer">
                              <IconButton
                                aria-label="duplicate"
                                className={classes.optionButtons}
                                onClick={(event) => {
                                  duplicateTask(event, list, index);
                                }}
                              >
                                <CopyIcon />
                              </IconButton>
                            </span>
                          </BootstrapTooltip>
                          <BootstrapTooltip
                            placement="top"
                            title={
                              list.assignedBy === props.currentUserEmail
                                ? "Delete Task"
                                : "Tasks added by others can't be deleted by you"
                            }
                          >
                            <span className="pointer">
                              <IconButton
                                aria-label="delete"
                                disabled={
                                  list.assignedBy === props.currentUserEmail
                                    ? false
                                    : true
                                }
                                className={classes.optionButtons}
                                onClick={(event) => {
                                  removeTodo(event, list, index);
                                }}
                              >
                                <DeleteIcon />
                              </IconButton>
                            </span>
                          </BootstrapTooltip>
                        </>
                      </>
                    )}
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails className="flex items-center justify-between space-x-2">
                    <span
                      style={{
                        wordBreak: "break-word",
                        overflowWrap: "breakWord",
                        wordWrap: "break-word",
                      }}
                      className="text-sm md:text-base xl:text-base lg:text-base"
                    >
                      <Linkify componentDecorator={componentDecorator}>
                        {list.detail}
                      </Linkify>
                    </span>                    
                    <span className="flex flex-col items-center justify-end w-auto space-y-2 md:w-96 md:space-y-0 md:flex-row md:flex md:space-x-2">
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          BUDGET
                        </span>
                        <span className="text-xl font-medium text-center rounded-full w-28 h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.estimatedBudget
                            ? `$ ${list.estimatedBudget}`
                            : `$ 0`}
                        </span>
                      </span>
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          URGENCY
                        </span>
                        <span className="w-24 text-xl font-semibold text-center rounded-full h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.urgent ? `${list.urgent} / 5` : `0 / 5`}
                        </span>
                      </span>
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          IMPACT
                        </span>
                        <span className="w-24 text-xl font-semibold text-center rounded-full h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.important ? `${list.important} / 5` : `0 / 5`}
                        </span>
                      </span>
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          DIFFICULTY
                        </span>
                        <span className="w-24 text-xl font-semibold text-center rounded-full h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.level ? `${list.level} / 5` : `0 / 5`}
                        </span>
                      </span>
                    </span>
                    {/* <Typography color="textSecondary" className={classes.taskProps}> 
                  {list.urgent}
                 </Typography> */}
                    {/* <Typography color="textSecondary" className={classes.taskProps}>
                   {list.important}
                  </Typography>
                  <Typography color="textSecondary" className={classes.taskProps}>
                  {list.level}
                  </Typography> */}
                  {
                    mobileWidth ?
                    <span className="col-center">
                      <span className="flex">
                            <Typography className={classes.dateField}>
                              {fullDate.date}
                              <br />
                              {fullDate.clock}
                            </Typography>
                        </span>
                        <BootstrapTooltip placement="top" title={list.assignedBy===props.currentUserEmail ? "Edit Task" : "Tasks added by others can't be edited by you"}>
                            <span className="pointer">
                              <IconButton aria-label="edit" disabled={list.assignedBy===props.currentUserEmail? false : true} className={classes.optionButtons} onClick={(event) => {
                                editTodo(event, list, index);
                              }}>
                                  <EditIcon/>
                              </IconButton>
                            </span>
                        </BootstrapTooltip>
                        <BootstrapTooltip placement="top" title="Duplicate Task">
                          <span className="pointer">
                            <IconButton aria-label="duplicate" className={classes.optionButtons} onClick={(event) => {
                              duplicateTask(event, list, index);
                            }}>
                              <CopyIcon />
                            </IconButton>
                          </span>
                        </BootstrapTooltip>
                        <BootstrapTooltip placement="top" title={list.assignedBy===props.currentUserEmail ? "Delete Task" : "Tasks added by others can't be deleted by you"}>
                            <span className="pointer">
                              <IconButton aria-label="delete" disabled={list.assignedBy===props.currentUserEmail? false : true} className={classes.optionButtons} onClick={(event) => {
                              removeTodo(event, list, index);
                              }}>
                                <DeleteIcon/>
                              </IconButton>
                            </span>
                        </BootstrapTooltip>
                      </span>
                      : null
                  }
                  </ExpansionPanelDetails>
                </ExpansionPanel>
              );
            })}
          </div>
        )}
        {props.list.todoList.length === 0 && (
          <div>
            <h5 style={{ textAlign: "center", padding: 25 }}>
              Add the first task/ step to
              <Typography variant="h6"> {props.list.name}</Typography> and start
              getting things todone around here...
            </h5>
          </div>
        )}
        <IconButton
          aria-label="edit"
          className={classes.addTodoItemButton}
          onClick={(event) => {
            props.openNewTodoDialog();
          }}
        >
          <AddCircleOutlineIcon className={classes.addTodoItemButtonIcon} />
        </IconButton>

        {/* ALERT FOR WHEN TASK IS MARKED COMPLETED */}
        {
          // in the below line, && is used to make a AND Gate meaning that it'll only display Box component if
          // openCompletedAlert is true (true+true=true & false in every other combination)
          // this is widely used instead of ternary operator when there is no value for "else" condition
          openCompletedAlert && (
            <Box
              position="fixed"
              width="250px"
              top="40%"
              left="36%"
              zIndex="999"
            >
              <SuccessAnimation color="#3dd19d" text={chooseAlertText()} />
            </Box>
          )
        }

        <Snackbar
          open={alertOpen}
          autoHideDuration={4000}
          onClose={handleCloseAlert}
          anchorOrigin={{ vertical: "top", horizontal: "left" }}
        >
          <Alert
            style={{ background: "#3dd19d" }}
            variant="filled"
            onClose={handleCloseAlert}
            severity={severity}
          >
            {alertMessage}
          </Alert>
        </Snackbar>
      </Container>
    );
  } else {
    return (
      <div>
        <h3
          style={{
            textAlign: "center",
            padding: 25,
            marginTop: "50px",
            minHeight: "40vh",
          }}
        >
          Create your first board from left menu and start getting things todone
          around here.
        </h3>
      </div>
    );
  }
}

export default TodoItemList;
