import React from "react";

import Card from '@material-ui/core/Card';

const MainFooter = () => {
  return (
    <>
      <span className="p-4 mt-20 mb-10 row-center">
        <Card elevation={2} className="px-3 py-2">
          <span className="text-base text-gray-600">
            Get started on your next task, idea, or project with todue boards.
            <a
              target="_blank"
              rel="noopener noreferrer"
              style={{ textDecoration: "none" }}
              href="https://forms.gle/CVBbiajoySqQtTgo8"
            >
              <span className="inline font-medium text-green">
                {" "}
                Join Email List
              </span>
            </a>
          </span>
        </Card>
      </span>
      <span className="flex justify-center mt-10 mb-5">
        <span className="text-gray-700">
          Copyright 2021. Follow us on twitter
          <a
            style={{ textDecoration: "none" }}
            className="ml-1 text-green"
            target="_blank"
            rel="noopener noreferrer"
            href="https://twitter.com/@todue1"
          >
            <span> @todue1</span>
            {/* @DannyMullan6  */}
          </a>
        </span>
      </span>
    </>
  );
};

export default MainFooter;
