import dayjs from "dayjs";
import isBetween from 'dayjs/plugin/isBetween';
dayjs.extend(isBetween);

const checkBwDaysFuture = (tsToCheck, daysInFuture) => {

    let futureTimeStamp = new Date();
    futureTimeStamp.setTime(futureTimeStamp.getTime() + (daysInFuture * 24 * 60 * 60 * 1000));
  
    const startDate = dateMaker(new Date().getTime());
    const endDate = dateMaker(futureTimeStamp);
    const dateToCheck = dateMaker(tsToCheck);
  
    return dayjs(dateToCheck).isBetween(startDate, dayjs(endDate));
};

const dateMaker = (timestamp) => {
    // '2020-10-19'
    const date = new Date(timestamp);
    const fullYear = date.getFullYear();
    const month = date.getMonth()+1;
    const day = date.getDate();
  
    return `${fullYear}-${month}-${day}`;
}

const TODAY_TS = 1624213693481;

const isDueToday = (timestamp) => dayjs().isSame(dateMaker(timestamp), 'day');

export const dueDateFilter = (tasks) => {
    const today = [];
    const tomorrow = [];
    const sevendays = [];
    const thirtydays = [];
    
    const incompleteTasks = tasks.filter(task=>!task.isComplete);
    incompleteTasks.map(task=>{
        if(isDueToday(task.date)) today.push(task);
        if(checkBwDaysFuture(task.date, 2)) tomorrow.push(task);
        if(checkBwDaysFuture(task.date, 7)) sevendays.push(task);
        if(checkBwDaysFuture(task.date, 30)) thirtydays.push(task);
    })

    return [today, tomorrow, sevendays, thirtydays];
}