import React, {useState} from 'react'

import Box from '@material-ui/core/Box';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import Snackbar from '@material-ui/core/Snackbar';
import InputLabel from '@material-ui/core/InputLabel';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';

import Alert from "@material-ui/lab/Alert";
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import withStyles from '@material-ui/core/styles/withStyles'
import { Link, withRouter } from 'react-router-dom'

import {makeStyles } from '@material-ui/core/styles';

import DeleteIcon from "@material-ui/icons/Delete";

import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import CheckCircleIcon from '@material-ui/icons/CheckCircle';

import { v4 as uuidv4 } from 'uuid';
import axios from 'axios';
import firebase from '../../services/firebase'

const styles = theme => ({
  main: {
    width: 'auto',
    display: 'block', // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
  backToLogin: {
    background: '#41b5f0',
    marginTop: '10px',
  },
})

function Register(props) {

  const {classes} = props

  const storage = firebase.storage();

  const [signupLoading, setSignupLoading] = useState(false);

  const [file, setFile] = useState(null);
  const [url, setURL] = useState("");
  
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [psudo, setPsudo] = useState('');
  const [password, setPassword] = useState('');
  const [position, setPosition] = useState('');

  const [successModalOpen, setSuccessModalOpen] = useState(false);

  const [alertOpen, setAlertOpen] = useState(false);
  const [severity, setSeverity] = useState('success');
  const [alertMessage, setAlertMessage] = useState('');
  const handleCloseAlert = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setAlertOpen(false);
  };

  // IMAGE UPLOAD INPUT
  const handleFileChange = async (e) => {
    const filee = e.target.files[0];
    await setFile(filee);
    handleUpload(filee);
  }

  const handleUpload= async (file) => {
    try{
    const fileName = await file.name;
    const uploadTask = storage.ref(`/images/${fileName}`).put(file);
    uploadTask.on("state_changed", {
      'complete': () => {
        storage
          .ref("images")
          .child(fileName)
          .getDownloadURL()
          .then((url) => {
            // setFile(null);
            setURL(url);
            console.log('URLLLLLLLLLLL', url);
          });
      }     
    });
  } catch(err){
    console.log('ERRORRRRRRRRRRR',err);
  }
  }

  const validateData = ()=>{
    if(name==="") return 'Please enter your full name';
    if(name.length>20) return 'Name can only be of 20 characters';
    if(!name.includes(' ')) return 'Please use your full name';
    if(psudo.length>20) return 'Username can only be of 20 characters';
    if(psudo.includes(' ')) return 'username cannot contain spaces';
    if(psudo.includes('`')||psudo.includes('!')||psudo.includes('@')||psudo.includes('#')||psudo.includes('$')||psudo.includes('%')||psudo.includes('^')||psudo.includes('&')||psudo.includes('*')||psudo.includes("'")||psudo.includes('/')||psudo.includes('\\')) return 'username cannot contain special characters except underscore: _ ';
    if(psudo==="") return 'Please enter a unique username';
    /*if(position==="") return 'Please enter your employment position';*/
    if(email==="") return 'Please enter a valid email address';
    // if(file===null) return 'Please upload your profile picture ';
    return true;
  }
  
  return (
    <main className={classes.main}>
      <Paper className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon/>
        </Avatar>
        <Typography component="h1" variant="h5">
          Create Account
        </Typography>
        <form className={classes.form} onSubmit={e => e.preventDefault() && false}>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="name">Full Name (first and last name)</InputLabel>
            <Input id="name" name="name" autoComplete="on" autoFocus value={name}
                   onChange={e => setName(e.target.value)}/>
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="username">Username (no spaces)</InputLabel>
            <Input id="username" name="username" autoComplete="on" value={psudo} onChange={e => setPsudo(e.target.value)}/>
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="email">Email Address</InputLabel>
            <Input id="email" name="email" autoComplete="on" value={email} onChange={e => setEmail(e.target.value)}/>
          </FormControl>
          <Typography>Profile Picture (only .jpg files)</Typography>
          <FormControl margin="normal" fullWidth>
            <InputLabel htmlFor="file">Profile Picture</InputLabel>
            <Input id="file" name="file" type="file" onChange={handleFileChange}/>
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input name="password" type="password" id="password" autoComplete="off" value={password}
                   onChange={e => setPassword(e.target.value)}/>
          </FormControl>

          {
              signupLoading ? 
                <Box display="flex" justifyContent="center" width="100%"><CircularProgress style={{color:"#3dd19d"}}/></Box> 
              : <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              onClick={onRegister}
              className={classes.submit}
              style={{textDecoration:'none', color: 'white'}}
              // disabled={signupLoading}
              >
              Register
            </Button>
          }
          
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="black"
            // component={Link}
            // to="/"
            style={{textDecoration: 'none'}}
            className={classes.backToLogin}
            >
            <Link to="/" style={{textDecoration:'none', color: 'white'}}>Go back to Login </Link>
          </Button>
        </form>
      </Paper>

      <SuccessModal open={successModalOpen} setOpen={setSuccessModalOpen} />

      <Snackbar
        open={alertOpen}
        autoHideDuration={4000}
        onClose={handleCloseAlert}
        // anchorOrigin={{vertical: 'top', horizontal:'center'}}
      >
        <Alert variant="filled" onClose={handleCloseAlert} severity={severity}>
          {alertMessage}
        </Alert>
      </Snackbar>
    </main>
  )

  
  async function onRegister() {

  const tempDate = new Date().getTime() + (3600*24*1000);
  const dateForTomorrow = new Date(tempDate).getTime();

  const buildTodoList = (email) => [
        {
          name: "Hi, welcome to Mytodue.com. I am a task. Mark me as completed!",
          detail: "",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },  
        {
          name: "Click anywhere or dropdown arrow to expand the view and see the details section.",
          detail: "You can add context, links to documents, milestones, and tips to support main goal/ objective.",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },
        {
          name: "Now it's time to create your first task and call it 'Test, check me!'. Then Mark as completed",
          detail: "",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },
        {
          name: "View your profile page by clicking your name/ profile picture in the top right",
          detail: "",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },
        {
          name: "ReadMe: Message to you from Danny",
          detail: "Hi, I wanted to write personal note thanking you for joining Todue. We are building this platform in public and in the beta testing stage. We are building Todue to help people with their remote work and lives. You can use it to create personal todo lists, or assign errands to family or friends, or delegate work flows and sequences to co-workers and colleagues. We spent most of the pandemic at home working on this project and early adopters like yourself make all the long nights worth it. I hope you enjoy it and it brings you affirmation and joy! Regards, Danny. You can email me anytime with ideas, questions, bugs at mytoduelist2020@gmail.com or follow me @danm2021 on twitter.",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },
        {
          name: "Help us spread the word",
          detail: "You can Todue spread the word by inviting a friend. Create a task called “Hi friend” to this welcome board & assign to a friend or co worker that would enjoy this platform (add email at the bottom of the task form). They will receive an email notification that you created it. When they create an account and complete the task  you will receive an email. You can invite as many friends to this board as you like.",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },
        {
          name: "Follow us on Twitter at @Todue1 (link in details section. Click drop down)",
          detail: "Follow us on Twitter at @Todue1 https://twitter.com/Todue1",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },
        {
          name: "If you complete this board...",
          detail: "If you complete this board you will be invited to our ideas and improvements board. This is exclusive behind the scenes board with ideas, updates, and brainstorming.",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        },
        {
          name: "Have early feedback? Fill out this survey",
          detail: "https://docs.google.com/forms/d/e/1FAIpQLSdVHZ_BLbBUL5LhSfGtTgoJkk_g9lzsDUA6LFWV-RQzUguzLg/viewform",
          date: dateForTomorrow,
          level: 1,
          important: 2,
          urgent: 3,
          isComplete: false,
          createDate: new Date().getTime(),
          assignedTo: email,
          assignedBy: email,
          taskId: uuidv4(),
          estimatedDays: 5,
          estimatedBudget: 5
        }
      ]
    
    const sendWelcomeEmail = (username, email) => {
      const url = 'https://us-central1-todue-mullan.cloudfunctions.net/sendWelcomeEmail';
      const functionData = {
        username: username,
        email: email,
      }
      axios.post(url, functionData).then((e)=>console.log(e)).catch(err=>console.log(err));
    }

    try {
      // await firebase.register(name, email, password)
      const validationResult = validateData();
      if(validationResult!==true){
        await setSeverity('error');
        await setAlertMessage(validationResult);
        await setAlertOpen(true);
        return;
      }
      // await firebase.auth().createUserWithEmailAndPassword(email, password)
      // await firebase.auth().currentUser.updateProfile({
      //   displayName: name
      // })
      // props.history.replace('/home')
      
      await setSignupLoading(true);
      
      firebase.firestore().collection('users')
        .where("psudo", "==", psudo)
        .get()
        .then(async (snapShot) => {
            // snapShot.map(async (doc) => {
              // if(doc.psudo===psudo){
                // console.log(snapShot)
              if(snapShot!==null && snapShot.size>0){
                await setSeverity('error');
                await setAlertMessage(`A user with username ${psudo} already exists`);
                await setAlertOpen(true);
                await setSignupLoading(false);
                return;
              }
              else {
                try{
                  await firebase.auth().createUserWithEmailAndPassword(email, password);
                  // await handleUpload();
                }
                catch (err){
                  await setSeverity('error');
                  await setAlertMessage(err.message);
                  await setAlertOpen(true);
                  await setSignupLoading(false);
                }
                if(firebase.auth().currentUser===null) return;
                await firebase.auth().currentUser.updateProfile({
                  displayName: psudo
                }).then( async ()=> {
                  // handleUpload().then(async ()=>{
                    // await handleUpload();
                    await firebase.auth().currentUser.sendEmailVerification();
                    const currentUserId = await firebase.auth().currentUser.uid;
                    // handleUpload().then(()=>
                      setTimeout(()=>{
                      firebase.firestore().collection('users').doc(currentUserId)
                      .set({
                        name: name,
                        email: email,
                        psudo: psudo,
                        position: position,
                        imgUrl: url,
                      })
                      .then(async (doc) => {

                        firebase
                          .firestore()
                          .collection('boards')
                          .add({
                            name: `Welcome, ${name?.split(' ')[0]} I am a board`,
                            todoList: buildTodoList(email),
                            createdBy: email,
                            createdByUsername: psudo,
                            assignments: [email],
                          })
                          .then(async (doc) => {
                            localStorage.setItem('selectedListId', doc.id);
                          }).catch(err=>console.log(err));


                        sendWelcomeEmail(psudo, email);


                        await setSuccessModalOpen(true);
                        // await setSeverity('success');
                        // await setAlertMessage('Log in to your email to verify, then return to https://mytodue.com/ to sign in.');
                        // await setAlertOpen(true);
                        await setSignupLoading(false);
                        setTimeout(()=> props.history.replace('/'), 6000) 
                    }).catch(async (err) =>{
                        await setSeverity('error');
                        await setAlertMessage(err.message);
                        await setAlertOpen(true);
                        await setSignupLoading(false);
                    })
                  }, 1000)
                    // );
                  // })
                })
              }
            })
    } catch (err){
      await setSeverity('error');
      await setAlertMessage(err.message);
      await setAlertOpen(true);
      await setSignupLoading(false);
    }
  }
}

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    display:'flex',
    flexDirection:'column',
    justifyContent:'center',
    alignItems:'center',
    backgroundColor: 'rgb(33,33,33)',
    // border: "2px solid #000",
    border: 'none',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(4, 4, 4),
  },
}));

const SuccessModal = ({open, setOpen}) => {
  const classes = useStyles();

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Modal
    style={{display:'flex',alignItems:'center',justifyContent:'center'}}
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 5000,
        }}
        closeAfterTransition
      >
        <Fade in={open}>
          <div className={classes.paper}>
          <Typography><CheckCircleIcon fontSize="large" style={{marginTop:'5px', color:'lightgreen'}} /></Typography> 
            <h3 id="transition-modal-title" style={{color: 'white'}}>
                Success!
            </h3>
            <div id="transition-modal-description" style={{color: 'white', paddingBottom:'10px'}}>
              <h5>
                Verify your email BEFORE signing in.
              </h5>
            </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
};

export default withRouter(withStyles(styles)(Register))
