import React, { useState, useEffect } from "react";

import Checkbox from "@material-ui/core/Checkbox";
import Container from "@material-ui/core/Container";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import IconButton from "@material-ui/core/IconButton";
import InputBase from "@material-ui/core/InputBase";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Snackbar from "@material-ui/core/Snackbar";
import Alert from "@material-ui/lab/Alert";

import {
  AddCircleOutline as AddCircleOutlineIcon,
  // AutorenewTwoTone,
  // ContactsOutlined,
  Delete as DeleteIcon,
  Edit as EditIcon,
  ExpandMore as ExpandMoreIcon,
  Search as SearchIcon,
  FileCopy as CopyIcon,
  Share,
} from "@material-ui/icons";

import TimeAgo from "react-timeago";
import Linkify from "react-linkify";

import BootstrapTooltip from "../../components/BootstrapTooltip";
import SuccessAnimation from "../../components/SuccessAnimation";
import useStyles from "../../components/todoItemListStyles";

import "../../components/style.css";

import calculateFullDate from "../../components/utils/twoDigitDateTextMaker";
import { Fade, Switch, TextField, Zoom } from "@material-ui/core";
import FilterButton from "./FilterButton";
import { dueDateFilter } from "./utils";

const AllTasks = (props) => {
  let lists = props?.list
    ?.map((boardObj, index) =>
      boardObj.todoList
        .filter((task) =>
          [task.assignedTo, task.assignedBy].includes(props.currentUserEmail)
        )
        .flat()
        .map((task) => {
          return {
            ...task,
            boardName: boardObj.name,
            boardId: boardObj.id,
            boardIndex: index,
            boardObj: JSON.parse(JSON.stringify(boardObj))
          };
        })
    )
    .flat();

  // useEffect(() => {
  //   lists = props?.list
  //   ?.map((boardObj, index) =>
  //     boardObj.todoList
  //       .filter((task) =>
  //         [task.assignedTo, task.assignedBy].includes(props.currentUserEmail)
  //       )
  //       .flat()
  //       .map((task) => {
  //         return {
  //           ...task,
  //           boardName: boardObj.name,
  //           boardId: boardObj.id,
  //           boardIndex: index,
  //           boardObj: JSON.parse(JSON.stringify(boardObj))
  //         };
  //       })
  //   )
  //   .flat();
  // }, [props.todoLists])
    
  // let filteredLists = [...lists];
  const [filteredTasks, setFilteredTasks] = useState([...lists]);

  // useEffect(() => {
  //   setFilteredTasks(prev=>[...lists]);
  // }, [lists.length])

  console.log("listsssssss", lists);

  // Snackbar ALERT
  const [alertOpen, setAlertOpen] = useState(false);
  const [severity, setSeverity] = useState("success");
  const [alertMessage, setAlertMessage] = useState("");
  const handleCloseAlert = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setAlertOpen(false);
  };

  const handleSnackbarOpen = (severity, message) => {
    setSeverity(severity);
    setAlertMessage(message);
    setAlertOpen(true);
  };
  //

  const mobileWidth = window.screen.availWidth < 700;

  const classes = useStyles();

  const [mainKey, setMainKey] = useState(123);

  const [openCompletedAlert, setOpenCompletedAlert] = useState(false);

  const [searchText, setSearchText] = useState("");
  const [sortCompleted, setSortCompleted] = useState(false);
  const [sortIncomplete, setSortIncomplete] = useState(false);
  const [sortAll, setSortAll] = useState(false);

  const [filterDue, setFilterDue] = useState(false);
  const [filterOverdue, setFilterOverdue] = useState(false);

  // TIME FILTERS
  const [filterToday, setFilterToday] = useState(true);
  const [filterTomorrow, setFilterTomorrow] = useState(false);
  const [filter7days, setFilter7days] = useState(false);
  const [filter30days, setFilter30days] = useState(false);

  const [tasksToday, tasksTomorrow, tasks7days, tasks30days] = dueDateFilter(lists);

  // console.log('tasksToday', tasksToday, 'tasksTomorrow', tasksTomorrow, 'tasks7days', tasks7days, 'tasks30days', tasks30days);
  
  //

  const [sortValue, setSortValue] = useState("due");
  // const [sortCreated, setSortCreated] = useState(false);
  // const [sortDue, setSortDue] = useState(true);

  const [filterAssigned, setFilterAssigned] = useState(false);
  const [filterAssignee, setFilterAssignee] = useState(false);

  let remaining = [];
  if (lists?.length) {
    remaining = lists.filter((list) => !list.isComplete);
  } else {
    remaining = [];
  }

  let notRemaining = [];
  if (lists?.length) {
    notRemaining = lists.filter((list) => list.isComplete);
  } else {
    notRemaining = [];
  }

  let allTasks = [];
  if (lists?.length) {
    // allTasks = props.list.map(item=>item.todoList).flat().filter((list) => list);
    allTasks = [...lists];
  } else {
    allTasks = [];
  }

  // this will randomly choose words from an array and return it to the SuccessAnimation component as text prop
  const chooseAlertText = () => {
    const sentences = [
      "You Rock!!",
      "Let's gooo!!",
      "Great Work!",
      "Done with that!",
      "Nice Work!",
      "Woohooo",
      "Well done!",
      "Great Job",
    ]; // add as many affirmations as you want
    const selectedText = sentences[Math.round(Math.random() * 3)]; // replace 3 with 1 less than the number of messages in the array above

    return selectedText;
  };

  const todayDate = new Date().getTime();

  // FILTER BY: DUE / OVERDUE & FILTER BY: COMPLETE / INCOMPLETE
  let dueRemaining = [];
  let overdueRemaining = [];
  let assigned = [];
  let assignee = [];

  if (lists?.length) {
    dueRemaining = remaining.filter((list) => new Date(list.date) > new Date());
    overdueRemaining = remaining.filter((list) => list.date < todayDate);

    assigned = lists.filter(
      (list) =>
        list.assignedTo !== props.currentUserEmail &&
        list.assignedTo !== "" &&
        list.assignedBy === props.currentUserEmail
    );
    assignee = lists.filter(
      (list) =>
        list.assignedBy !== props.currentUserEmail &&
        list.assignedTo === props.currentUserEmail
    );
  }

  useEffect(() => {
    if (lists?.length) {
      
      if(filterToday) setFilteredTasks(tasksToday);
      else if(filterTomorrow) setFilteredTasks(tasksTomorrow);
      else if(filter7days) setFilteredTasks(tasks7days);
      else if(filter30days) setFilteredTasks(tasks30days);
      else if (filterDue) {
        setFilteredTasks(dueRemaining);
      } else if (filterOverdue) {
        setFilteredTasks(overdueRemaining);
      } else if (sortCompleted) {
        setFilteredTasks(lists.filter((list) => list.isComplete));
      } else if (sortIncomplete) {
        setFilteredTasks(lists.filter((list) => !list.isComplete));
      } else if (filterAssigned) {
        setFilteredTasks(assigned);
      } else if (filterAssignee) {
        setFilteredTasks(assignee);
      } else
        setFilteredTasks(
          lists.filter(
            (list) =>
              list.name
                .toLowerCase()
                .includes(searchText.toLocaleLowerCase()) ||
              list.detail.toLowerCase().includes(searchText.toLocaleLowerCase())
          )
        );
    }
  }, [
    filterDue,
    filterOverdue,
    sortCompleted,
    sortIncomplete,
    filterAssigned,
    filterAssignee,
    searchText,
    filterToday,
    filterTomorrow,
    filter7days,
    filter30days,
  ]);

  // SORT BY:   CREATE DATE / DUE DATE
  const handleChange = (event) => {
    setSortValue(event.target.value);

    if (event.target.value === "created") {
      setFilteredTasks((prev) =>
        prev.sort((a, b) => a.createDate - b.createDate)
      );
    } else if (event.target.value === "due") {
      setFilteredTasks((prev) => prev.sort((a, b) => a.date - b.date));
    }
  };

  const sortByAll = async () => {

    await setSortAll(true);

    await setFilterAssigned(false);
    await setFilterAssignee(false);
    await setSortIncomplete(false);
    await setSortCompleted(false);
    await setFilterDue(false);
    await setFilterOverdue(false);

    await setFilterToday(false);
    await setFilterTomorrow(false);
    await setFilter7days(false);
    await setFilter30days(false);
  };

  const editTodo = (event, list, index) => {
    event.stopPropagation();
    props.openEditDialog(list, index);
  };

  const removeTodo = (event, list, index) => {
    event.stopPropagation();
    const deletedTask = lists.splice(index, 1);
    const assignedToo = deletedTask[0].assignedTo;
    const assignedByy = deletedTask[0].assignedBy;
    // console.log('DELTED TASK:', deletedTask);
    // console.log('assigned by:', assignedByy, 'assigned To:', assignedToo);
    if (
      assignedToo !== props.currentUserEmail &&
      assignedToo !== "" &&
      assignedByy === props.currentUserEmail
    ) {
      props.removeNewAssignment(
        props.selectedList.id,
        props.selectedList.name,
        deletedTask[0]
      );
    }
    props.updateTodoList({ ...props.list, todoList: lists });
    setMainKey((mainKey) => mainKey + 1);
  };

  const checkboxToggle = async (event, task) => {
    if (event.target.tagName !== "INPUT") return;

    event.stopPropagation();

    const updatedTodoList = task.boardObj.todoList.map(item=>
      {
        if(item.taskId===task.taskId) {
          return { ...item, isComplete: !task.isComplete, completedAt: new Date().getTime() }
        }
        return item;
      });

    const updatedBoardObj = {...task.boardObj, todoList: updatedTodoList};
    const updatedTask = { ...task, boardObj: updatedBoardObj, isComplete: !task.isComplete };

    // lists = [...lists, updatedTask];
    // sortByAll();
    // setFilteredTasks(prev=> [...prev, updatedTask])

    // open success message
    if (updatedTask.isComplete) {
      await setOpenCompletedAlert(true);
      setTimeout(async () => await setOpenCompletedAlert(false), 4000);
      // the function inside setTimeout will run after 4000 millisecond (written as second argument)
      // which means that animation message will be open for 4000ms (or 4 seconds)

      handleSnackbarOpen('success', 'Great, the task has been moved to the Todone section');
    }

    if (task.assignedBy !== props.currentUserEmail && task.isComplete) {
      console.log("RAN CHECKKKK");
      const data = {
        ...task.boardObj,
        todoList: [...updatedTodoList]
      };
      console.log('data', data);
      props.updateTodoList(data, true, task.assignedTo, task.assignedBy);
    } else {
      props.updateTodoList({
        ...task.boardObj,
        todoList: [...updatedTodoList]
      });
      // console.log('data', {
        // ...task.boardObj,
        // todoList: [...updatedTodoList]
      // });
    }

    const filteredTodoLists = props.list.filter(item=>item.id!==updatedBoardObj.id);
    props.setTodoLists(prev=>[...filteredTodoLists, updatedBoardObj]);

    const filteredLists = lists.filter(item=>item.taskId!==task.taskId);
    lists = [...filteredLists, updatedTask].sort((a,b)=>a.date-b.date);
    // console.log('lists', lists);
    // setFilteredTasks(prev=> lists);
    setFilteredTasks(prev=>prev.map(item=>item.taskId===updatedTask.taskId ? updatedTask : item));
    // sortByAll();

    setMainKey((mainKey) => mainKey + 1) // for remounting the component (https://www.nikgraf.com/blog/using-reacts-key-attribute-to-remount-a-component)
  };

  const componentDecorator = (href, text, key) => (
    <a href={href} key={key} target="_blank" rel="noopener noreferrer">
      {text}
    </a>
  );

  const loc = window.location;

  const animationTimeout = 500;

  if (props?.list?.length) {
    return (
      <Container key={mainKey} maxWidth="lg" className={classes.container}>
        <Grid container spacing={1}>
          <span className="w-auto mt-1 row-center">
            <Zoom timeout={animationTimeout} in={true}>
              <Typography aphy variant="h6" className={classes.listTitle}>
                All My Tasks
              </Typography>
            </Zoom>
          </span>
        </Grid>
        <Grid container spacing={1}>
          <Grid item height={50} xs={12} sm={12} lg={12}>
            <FilterButton
              name="Today"
              state={filterToday}
              stat={tasksToday.length}
              onClick={async () => {
                // compensate
                await setSortAll(false);
                
                await setFilterAssigned(false);
                await setFilterAssignee(false);

                await setSortIncomplete(false);
                await setSortCompleted(false);
                await setFilterDue(false);
                await setFilterOverdue(false);
                
                await setFilterToday(true);
                await setFilterTomorrow(false);
                await setFilter7days(false);
                await setFilter30days(false);
              }}
            />
            <FilterButton
              name="Tomorrow"
              state={filterTomorrow}
              stat={tasksTomorrow.length}
              onClick={async () => {
                // compensate
                await setSortAll(false);
                
                await setFilterAssigned(false);
                await setFilterAssignee(false);

                await setSortIncomplete(false);
                await setSortCompleted(false);
                await setFilterDue(false);
                await setFilterOverdue(false);

                await setFilterToday(false);
                await setFilterTomorrow(true);
                await setFilter7days(false);
                await setFilter30days(false);
              }}
            />
            <FilterButton
              name="Next Seven Days"
              state={filter7days}
              stat={tasks7days.length}
              onClick={async () => {
                // compensate
                await setSortAll(false);
                
                await setFilterAssigned(false);
                await setFilterAssignee(false);

                await setSortIncomplete(false);
                await setSortCompleted(false);
                await setFilterDue(false);
                await setFilterOverdue(false);

                await setFilterToday(false);
                await setFilterTomorrow(false);
                await setFilter7days(true);
                await setFilter30days(false);
              }}
            />
            <FilterButton
              name="Next 30 Days"
              state={filter30days}
              stat={tasks30days.length}
              onClick={async () => {
                // compensate
                await setSortAll(false);
                
                await setFilterAssigned(false);
                await setFilterAssignee(false);

                await setSortIncomplete(false);
                await setSortCompleted(false);
                await setFilterDue(false);
                await setFilterOverdue(false);

                await setFilterToday(false);
                await setFilterTomorrow(false);
                await setFilter7days(false);
                await setFilter30days(true);
              }}
            />
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                onClick={sortByAll}
                className="no-outline"
                style={
                  sortAll
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
              >
                <Typography
                  style={
                    sortAll
                      ? { fontWeight: "bold" }
                      : null
                  }
                >
                  All Time
                </Typography>
                <Typography variant="h6">
                  <Typography
                    style={{ paddingLeft: "15px" }}
                    display="inline"
                    variant="h6"
                    color="primary"
                  >
                    {" "}
                    {allTasks.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <FilterButton
              name="Todue"
              state={sortIncomplete}
              stat={remaining.length}
              onClick={async () => {
                // compensate
                await setSortAll(false);
                
                await setFilterAssigned(false);
                await setFilterAssignee(false);

                await setSortIncomplete(true);
                await setSortCompleted(false);
                await setFilterDue(false);
                await setFilterOverdue(false);

                await setFilterToday(false);
                await setFilterTomorrow(false);
                await setFilter7days(false);
                await setFilter30days(false);
              }}
            />
            <FilterButton
              name="Todone"
              state={sortCompleted}
              stat={notRemaining.length}
              onClick={async () => {
                // compensate
                await setSortAll(false);
                
                await setFilterAssigned(false);
                await setFilterAssignee(false);

                await setSortCompleted(true);
                await setSortIncomplete(false);
                await setFilterDue(false);
                await setFilterOverdue(false);

                await setFilterToday(false);
                await setFilterTomorrow(false);
                await setFilter7days(false);
                await setFilter30days(false);
              }}
            />
            <FilterButton
              name="Assigned to others"
              state={filterAssigned}
              stat={assigned.length}
              onClick={async () => {
                await setSortAll(false);
                
                await setFilterAssigned(true);
                await setFilterAssignee(false);
                await setSortIncomplete(false);
                await setSortCompleted(false);
                await setFilterDue(false);
                await setFilterOverdue(false);

                await setFilterToday(false);
                await setFilterTomorrow(false);
                await setFilter7days(false);
                await setFilter30days(false);
              }}
            />
            <FilterButton
              name="Assigned to me"
              state={filterAssignee}
              stat={assignee.length}
              onClick={async () => {
                await setSortAll(false);
                
                await setFilterAssigned(false);
                await setFilterAssignee(true);
                await setFilterDue(false);
                await setFilterOverdue(false);
                await setSortCompleted(false);
                await setSortIncomplete(false);

                await setFilterToday(false);
                await setFilterTomorrow(false);
                await setFilter7days(false);
                await setFilter30days(false);
              }}
            />
            <Zoom timeout={animationTimeout} in={true}>
              <Button
                color="black"
                className="no-outline"
                style={
                  filterOverdue
                    ? {
                        borderBottom: "3px solid #3dd19d",
                        textTransform: "none",
                      }
                    : {
                        textTransform: "none",
                        borderBottom: "3px solid transparent",
                      }
                }
                onClick={async () => {
                  // compensation
                  await setSortAll(false);
                  
                  await setFilterAssigned(false);
                  await setFilterAssignee(false);

                  await setFilterDue(false);
                  await setFilterOverdue(true);
                  await setSortIncomplete(false);
                  await setSortCompleted(false);

                  await setFilterToday(false);
                  await setFilterTomorrow(false);
                  await setFilter7days(false);
                  await setFilter30days(false);
                }}
              >
                <Typography
                  style={filterOverdue ? { fontWeight: "bold" } : null}
                >
                  Overdue
                </Typography>{" "}
                <Typography variant="h6">
                  <Typography
                    style={
                      overdueRemaining.length > 0
                        ? { paddingLeft: "5px", color: "red" }
                        : { paddingLeft: "5px", color: "#3dd19d" }
                    }
                    display=""
                    variant="h6"
                  >
                    {" "}
                    {overdueRemaining.length}
                  </Typography>
                </Typography>
              </Button>
            </Zoom>
            <Zoom timeout={animationTimeout} in={true}>
              <FormControl
                style={{ marginTop: "3px", marginLeft: "10px" }}
                component="fieldset"
              >
                {/* <FormLabel component="legend">Sort By:</FormLabel> */}
                <RadioGroup
                  style={{ display: "flex", flexDirection: "row" }}
                  aria-label="sort by"
                  name="sortCreateDue"
                  value={sortValue}
                  onChange={handleChange}
                >
                  <FormControlLabel
                    style={{ fontSize: "0.9rem" }}
                    value="created"
                    control={<Radio />}
                    label="Date Created"
                  />
                  <FormControlLabel
                    style={{ fontSize: "0.9rem" }}
                    value="due"
                    control={<Radio />}
                    label="Due Date"
                  />
                </RadioGroup>
              </FormControl>
            </Zoom>
            <div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon style={{ color: "#3dd19d" }} />
              </div>
              <InputBase
                style={{ marginTop: "10px" }}
                placeholder="Search your tasks…"
                onChange={(text) => {
                  setSearchText(text.target.value);
                }}
                value={searchText}
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
                inputProps={{ "aria-label": "search" }}
              />
            </div>
          </Grid>
        </Grid>
        {filteredTasks?.length ? (
          <div className={classes.root}>
            {filteredTasks.map((list, index) => {
              const fullDate = calculateFullDate(list.date);
              return (
                // <Fade in={true}>
                <ExpansionPanel
                  // expanded={}
                  key={list.taskId}
                >
                  <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />} // !mobileWidth &&
                    aria-label="Expand"
                    aria-controls="additional-actions1-content"
                    id="additional-actions1-header"
                    classes={{
                      content: classes.expansionPanelSummaryContent,
                    }}
                  >
                    {mobileWidth ? (
                      <span className="flex flex-col justify-center w-full">
                        <FormControlLabel
                          style={{ pointerEvents: "none" }}
                          className={`${classes.todoItemFormControl} py-3 px-1`}
                          aria-label="Acknowledge"
                          onFocus={(event) => event.stopPropagation()}
                          control={
                            <Checkbox
                              style={{ pointerEvents: "auto" }}
                              onClick={(event) => {
                                checkboxToggle(event, list);
                              }}
                              checked={list.isComplete}
                              disabled={
                                list.assignedBy === props.currentUserEmail ||
                                list.assignedTo === props.currentUserEmail
                                  ? false
                                  : true
                              }
                            />
                          }
                          label={list.name}
                          style={{ width: "100%", color: "black" }}
                        />

                        <span className="space-x-1 row-center">
                          {props.isLogged &&
                          list.assignedTo === props.currentUserEmail &&
                          list.assignedBy !== props.currentUserEmail ? (
                            <Typography
                              className="pb-1 pl-1 col-center"
                              variant="subtitle2"
                            >
                              <span>Assigned By:</span>
                              <span>{list.assignedBy}</span>
                            </Typography>
                          ) : props.isLogged &&
                            list.assignedTo &&
                            list.assignedBy === props.currentUserEmail ? (
                            <Typography
                              className="pb-1 pl-1 col-center"
                              variant="subtitle2"
                            >
                              <span>Assigned To:</span>
                              <span>{list.assignedTo}</span>
                            </Typography>
                          ) : null}
                          <BootstrapTooltip
                            placement="top"
                            title="Sum of urgency, impact and difficulty"
                          >
                            <span className="ml-1 min-w-max col-center">
                              {/* <span className="text-sm">Todue Points</span> */}
                              <span className="px-2 py-1 min-w-min rounded-2xl bg-green-lightest">
                                {1 +
                                  (typeof list.level === "number"
                                    ? list.level
                                    : 1) +
                                  (typeof list.urgent === "number"
                                    ? list.urgent
                                    : 1) +
                                  (typeof list.important === "number"
                                    ? list.important
                                    : 1)}
                              </span>
                            </span>
                          </BootstrapTooltip>
                        </span>
                        <span className="row-center">
                          <Box
                            component="span"
                            className="flex flex-wrap w-24"
                            pl={2}
                            pt={2}
                          >
                            <Typography
                              variant="subtitle2"
                              style={
                                list.date < todayDate && list.isComplete
                                  ? { color: "black" }
                                  : list.date < todayDate && !list.isComplete
                                  ? { color: "red" }
                                  : { color: "green" }
                              }
                            >
                              <TimeAgo
                                live={false} // use false to increase performance (live means it updates est. time automatically without reloading the page)
                                date={list.date}
                              />
                            </Typography>
                          </Box>
                          <span className="flex">
                            <Typography className={classes.dateField}>
                              {fullDate.date}
                              <br />
                              {fullDate.clock}
                            </Typography>

                            {/* <>
                              <BootstrapTooltip
                                placement="top"
                                title={
                                  list.assignedBy === props.currentUserEmail
                                    ? "Edit Task"
                                    : "Tasks added by others can't be edited by you"
                                }
                              >
                                <span className="pointer">
                                  <IconButton
                                    aria-label="edit"
                                    disabled={
                                      list.assignedBy === props.currentUserEmail
                                        ? false
                                        : true
                                    }
                                    className={classes.optionButtons}
                                    onClick={(event) => {
                                      editTodo(event, list, index);
                                    }}
                                  >
                                    <EditIcon />
                                  </IconButton>
                                </span>
                              </BootstrapTooltip>
                              <BootstrapTooltip
                                placement="top"
                                title={
                                  list.assignedBy === props.currentUserEmail
                                    ? "Delete Task"
                                    : "Tasks added by others can't be deleted by you"
                                }
                              >
                                <span className="pointer">
                                  <IconButton
                                    aria-label="delete"
                                    disabled={
                                      list.assignedBy === props.currentUserEmail
                                        ? false
                                        : true
                                    }
                                    className={classes.optionButtons}
                                    onClick={(event) => {
                                      removeTodo(event, list, index);
                                    }}
                                  >
                                    <DeleteIcon />
                                  </IconButton>
                                </span>
                              </BootstrapTooltip>
                            </> */}
                          </span>
                        </span>
                      </span>
                    ) : (
                      <>
                        {
                          <Fade in={true}>
                            <FormControlLabel
                              style={{
                                pointerEvents: "none",
                                transitionDelay: "800ms",
                              }}
                              className={classes.todoItemFormControl}
                              aria-label="Acknowledge"
                              onFocus={(event) => event.stopPropagation()}
                              control={
                                <Checkbox
                                  style={{ pointerEvents: "auto" }}
                                  onClick={(event) => {
                                    checkboxToggle(event, list);
                                  }}
                                  checked={list.isComplete}
                                  disabled={
                                    list.assignedBy ===
                                      props.currentUserEmail ||
                                    list.assignedTo === props.currentUserEmail
                                      ? false
                                      : true
                                  }
                                />
                              }
                              label={list.name}
                            ></FormControlLabel>
                          </Fade>
                        }

                        <span className="flex-wrap w-1/3 col-center">
                          <span>Board</span>
                          <BootstrapTooltip placement="top" title="View Board">
                            <button
                              className="text-blue-500 no-outline hover:text-blue-700"
                              onClick={() => {
                                props.setOpenProfile(false);
                                props.setSelectedList(
                                  props.list[list.boardIndex]
                                );
                                props.setOpenTasksFeed(false);
                                props.setOpenDrawer(true);
                              }}
                            >
                              {list.boardName}
                            </button>
                          </BootstrapTooltip>
                        </span>

                        <BootstrapTooltip
                          placement="top"
                          title="Sum of urgency, impact and difficulty"
                        >
                          <span className="ml-1 mr-3 space-x-1 min-w-max row-center">
                            {/* <span className="">Todue Points</span> */}
                            <span className="text-green-dark min-w-min">
                              {1 +
                                (typeof list.level === "number"
                                  ? list.level
                                  : 1) +
                                (typeof list.urgent === "number"
                                  ? list.urgent
                                  : 1) +
                                (typeof list.important === "number"
                                  ? list.important
                                  : 1)}
                            </span>
                            <span>points</span>
                          </span>
                        </BootstrapTooltip>

                        {props.isLogged &&
                        list.assignedTo &&
                        list.assignedBy !== props.currentUserEmail ? (
                          <Typography className="w-1/3" variant="subtitle2">
                            Assigned By:
                            <BootstrapTooltip
                              placement="top"
                              title="View Profile"
                            >
                              <button
                                className="text-blue-500 no-outline hover:text-blue-700"
                                onClick={() => {
                                  props.setOpenProfile(true);
                                  props.setProfileUserId(list.assignedBy);
                                  props.setOpenTasksFeed(false);
                                }}
                              >
                                {list.assignedBy}
                              </button>
                            </BootstrapTooltip>
                          </Typography>
                        ) : props.isLogged &&
                          list.assignedTo &&
                          list.assignedBy === props.currentUserEmail ? (
                          <Typography className="w-1/3" variant="subtitle2">
                            Assigned To:
                            <BootstrapTooltip
                              placement="top"
                              title="View Profile"
                            >
                              <button
                                className="text-blue-500 no-outline hover:text-blue-700"
                                onClick={() => {
                                  props.setOpenProfile(true);
                                  props.setProfileUserId(list.assignedTo);
                                  props.setOpenTasksFeed(false);
                                }}
                              >
                                {list.assignedTo}
                              </button>
                            </BootstrapTooltip>
                          </Typography>
                        ) : null}

                        <Box component="span" width="200px" pl={2} pt={2}>
                          <Typography
                            variant="subtitle2"
                            style={
                              list.date < todayDate && list.isComplete
                                ? { color: "black" }
                                : list.date < todayDate && !list.isComplete
                                ? { color: "red" }
                                : { color: "green" }
                            }
                          >
                            <TimeAgo
                              live={false} // use false to increase performance (live means it updates est. time automatically without reloading the page)
                              date={list.date}
                            />
                          </Typography>
                        </Box>

                        <Typography className={`${classes.dateField} mr-4`}>
                          {fullDate.date} {fullDate.clock}
                        </Typography>

                        {/* <>
                          <BootstrapTooltip
                            placement="top"
                            title={
                              list.assignedBy === props.currentUserEmail
                                ? "Edit Task"
                                : "Tasks added by others can't be edited by you"
                            }
                          >
                            <span className="pointer">
                              <IconButton
                                aria-label="edit"
                                disabled={
                                  list.assignedBy === props.currentUserEmail
                                    ? false
                                    : true
                                }
                                className={classes.optionButtons}
                                onClick={(event) => {
                                  editTodo(event, list, index);
                                }}
                              >
                                <EditIcon />
                              </IconButton>
                            </span>
                          </BootstrapTooltip>
                          <BootstrapTooltip
                            placement="top"
                            title={
                              list.assignedBy === props.currentUserEmail
                                ? "Delete Task"
                                : "Tasks added by others can't be deleted by you"
                            }
                          >
                            <span className="pointer">
                              <IconButton
                                aria-label="delete"
                                disabled={
                                  list.assignedBy === props.currentUserEmail
                                    ? false
                                    : true
                                }
                                className={classes.optionButtons}
                                onClick={(event) => {
                                  removeTodo(event, list, index);
                                }}
                              >
                                <DeleteIcon />
                              </IconButton>
                            </span>
                          </BootstrapTooltip>
                        </> */}
                      </>
                    )}
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails className="flex items-center justify-between space-x-2">
                    <span
                      style={{
                        wordBreak: "break-word",
                        overflowWrap: "breakWord",
                        wordWrap: "break-word",
                      }}
                      className="text-sm md:text-base xl:text-base lg:text-base"
                    >
                      <Linkify componentDecorator={componentDecorator}>
                        {list.detail}
                      </Linkify>
                    </span>
                    <span className="flex flex-col items-center justify-end w-auto space-y-2 md:w-96 md:space-y-0 md:flex-row md:flex md:space-x-2">
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          BUDGET
                        </span>
                        <span className="text-xl font-medium text-center rounded-full w-28 h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.estimatedBudget
                            ? `$ ${list.estimatedBudget}`
                            : `$ 0`}
                        </span>
                      </span>
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          URGENCY
                        </span>
                        <span className="w-32 text-xl font-semibold text-center rounded-full h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.urgent ? `${list.urgent} / 5` : `0 / 5`}
                        </span>
                      </span>
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          IMPACT
                        </span>
                        <span className="w-24 text-xl font-semibold text-center rounded-full h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.important ? `${list.important} / 5` : `0 / 5`}
                        </span>
                      </span>
                      <span className="col-center">
                        <span className="mb-1 text-xs font-semibold text-gray-500">
                          DIFFICULTY
                        </span>
                        <span className="w-24 text-xl font-semibold text-center rounded-full h-7 bg-opacity-80 bg-green-lightest text-green">
                          {list.level ? `${list.level} / 5` : `0 / 5`}
                        </span>
                      </span>
                    </span>
                  </ExpansionPanelDetails>
                </ExpansionPanel>
                // </Fade>
              );
            })}
          </div>
        ) : 
        <div>
          <h5 style={{ textAlign: "center", padding: 25 }}>
            No task matching this filter
          </h5>
        </div>
      }
        {props?.list?.length === 0 && (
          <div>
            <h5 style={{ textAlign: "center", padding: 25 }}>
              Add the first task/ step to
              <Typography variant="h6"> {props.list.name}</Typography> and start
              getting things todone around here...
            </h5>
          </div>
        )}

        {/* ALERT FOR WHEN TASK IS MARKED COMPLETED */}
        {
          // in the below line, && is used to make a AND Gate meaning that it'll only display Box component if
          // openCompletedAlert is true (true+true=true & false in every other combination)
          // this is widely used instead of ternary operator when there is no value for "else" condition
          openCompletedAlert && (
            <Box
              position="fixed"
              width="250px"
              top="40%"
              left="36%"
              zIndex="999"
            >
              <SuccessAnimation color="#3dd19d" text={chooseAlertText()} />
            </Box>
          )
        }

        <Snackbar
          open={alertOpen}
          autoHideDuration={4000}
          onClose={handleCloseAlert}
          anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
        >
          <Alert
            style={{backgroundColor: '#3dd19d'}}
            variant="filled"
            onClose={handleCloseAlert}
            severity={severity}
          >
            {alertMessage}
          </Alert>
        </Snackbar>
      </Container>
    );
  } else {
    return (
      <div>
        <h3
          style={{
            textAlign: "center",
            padding: 25,
            marginTop: "50px",
            minHeight: "40vh",
          }}
        >
          Create your first board from left menu and start getting things todone
          around here.
        </h3>
      </div>
    );
  }
};

export default AllTasks;
