function useTodoLists(currentUserId, setBoardsLoading, isLogged) {
  const [todoLists, setTodoLists] = React.useState([]);

  // useEffect(() => {
  //   firebase
  //     .firestore()
  //     .collection(currentUserId)
  //     .onSnapshot((snapShot) => {
  //       const newTodoLists = [];
  //       snapShot.docs.map((doc) => {
  //         let tempData = {};
  //         tempData = doc.data();
  //         tempData.id = doc.id;
  //         newTodoLists.push(tempData);
  //       })
  //       setTodoLists(newTodoLists);
  //     })
  // }, []);
  useEffect(() => {
    if(!isLogged) return;
    setBoardsLoading(true);
    firebase
      .firestore()
      .collection('boards').where('assignments','array-contains', currentUserId)
      .get()
      .then((snapShot) => {
        const newTodoLists = [];
        snapShot.docs.map((doc) => {
          let tempData = {};
          tempData = doc.data();
          tempData.id = doc.id;
          newTodoLists.push(tempData);
        })
        setTodoLists(newTodoLists);
        setBoardsLoading(false);
      }).catch((error) => {
        setBoardsLoading(false);
        alert('error getting documents');
        console.log("Error getting documents: ", error);
      });
  }, []);
  return todoLists;
}

