  const addNewAssignment = async (id,name, data, application="simple")=> {
    // const filteredToDoList = list.todoList.filter((obj)=>obj.assignedTo)
    // prev data model:
    // const collection = application==='simple' ? data.assignedTo : data.assignedBy; 
    const collection = 'boards';

    let sendAssignedTaskEmail = data.assignedTo!==currentUserEmail ? true : false;
    
    let previousData;
      previousData = await firebase.firestore().collection(collection).doc(id).get().then(doc=>{
        if(doc.exists) {return doc.data()} 
        else {
          return null
        };
      });
    // console.log('prev data',previousData, typeof(previousData.todoList));
    if(previousData){
      
      // let previousTask = previousData.todoList.filter(obj=> obj.taskId===data.taskId)[0]
      
      let previousTodoListFiltered = previousData.todoList.filter(obj=> obj.taskId!==data.taskId)
      previousData.todoList = previousTodoListFiltered;
      
      // console.log('previous assigned::',previousTask.assignedTo);
      // console.log('new Assigned:: ',data.assignedTo);
      // if(previousTask.assignedTo===data.assignedTo) {sendAssignedTaskEmail=false};
      
      previousData.todoList.push(data);
    }
    const todoList = previousData ? previousData.todoList : new Array(data);
    // const todoList = new Array(data);

    // Manage Assignments
    let assignmentsAll = [];
    assignmentsAll.push(previousData.createdBy); // so that board without tasks is also fetched by WHERE query
    previousData.todoList.map((task)=>{
      assignmentsAll.push(task.assignedBy);
      assignmentsAll.push(task.assignedTo);
    })
    const assignments = [... new Set(assignmentsAll)];
    // --------END-------

    firebase
      .firestore()
      .collection(collection)
      .doc(id)
      .set({
        createdBy: previousData.createdBy,
        createdByUsername: previousData.createdByUsername ? previousData.createdByUsername : '',
        name: name,
        todoList: todoList,
        assignments: assignments,
        isPublic: previousData.isPublic ? true : false
      })
      .then((e) => {
        if(sendAssignedTaskEmail){
          const url = 'https://us-central1-todue-mullan.cloudfunctions.net/sendTaskAssignEmail';
          const functionData = {
            username: currentUserName,
            email: data.assignedTo,
          }
          axios.post(url, functionData).then((e)=>console.log(e)).catch(err=>console.log(err));
        }
      })
      .catch(err=>console.log(err));
  }

