import React, { useState, useEffect, useRef, lazy, Suspense } from 'react';

import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { CircularProgress } from "@material-ui/core";
// import Chart from 'react-google-charts';

import { statsMaker } from './utils';

const Charts = (props) => {

    const [tabOpenNum, setTabOpenNum] = useState(2);
    const converters = {
        epochDaysToDate : (days) => new Date(Math.floor(18725 * 8.64e7)),
        // epochDaysToDate : (days) => new Date(Math.floor(18725 * 8.64e7)), // epochmonth - ((current year number - 1970 - 1) * 12) = current month number
        dateToDay : (date) => new Date(date).getDate(), 
    };
    return (
        <span className="h-full">
            <ChartTabs {...props} tabOpenNum={tabOpenNum} setTabOpenNum={setTabOpenNum} />
        </span>
    )
}

const ChartTabs = (props) => {

  const Chart = lazy(()=> import('react-google-charts'));

  const wrapper = useRef();
    useEffect(() => {
        return () => {
          wrapper?.current?.getChart()?.clearChart();
        };
    }, []);

    const handleChange = (event, newValue) => {
      props.setTabOpenNum(newValue);
    };

    const [chartData, setChartData] = useState([]);

    useEffect(() => {
      if(props.tasksData?.length){
        const data = statsMaker(props.tasksData, props.currentUserId);
        if(data) setChartData(data);
      }
    }, [chartData.length])
  
    return (
      <Paper square elevation={0}>
        {/* {console.log('chartData',chartData)} */}
        <Tabs
          value={props.tabOpenNum}
          indicatorColor="primary"
          textColor="primary"
          onChange={handleChange}
          aria-label="Chart Tabs"
          className="mb-3"
        >
          <Tab label="Daily" />
          <Tab label="Weekly" />
          <Tab label="Monthly" />
        </Tabs>
        <TabPanel value={props.tabOpenNum} index={0}>
            <Suspense fallback={<CircularProgress style={{ color: "#3dd19d" }} />}>
            <Chart
                width={'450px'}
                height={'320px'}
                chartType="Bar"
                loader={<CircularProgress style={{ color: "#3dd19d" }} />}
                data={
                  chartData.daily
                  // [
                  // ['Day', 'Completed', 'Created'],
                  // ['Mon', 0, 0],
                  // ['Tue', 60, 50],
                  // ['Wed', 70, 50],
                  // ['Thu', 80, 50],
                  // ['Fri', 100, 60],
                  // ['Sat', 60, 50],
                  // ['Sun', 70, 50]
                  // ]
                }
                options={{
                // Material design options
                colors: ['#3dd19d','lightblue', '#ffab91'],
                bar: { groupWidth: "60px" },
                chart: {
                    title: 'Daily Performance',
                    subtitle: 'Tasks completion metrics',
                },
                }}
                getChartWrapper={(w) => (wrapper.current = w)}
            />
            </Suspense>
        </TabPanel>
        <TabPanel value={props.tabOpenNum} index={1}> 
          <Suspense fallback={<CircularProgress style={{ color: "#3dd19d" }} />}>
            <Chart
                width={'450px'}
                height={'320px'}
                chartType="Bar"
                loader={<CircularProgress style={{ color: "#3dd19d" }} />}
                data={
                  chartData.weekly
                  // [
                  //   ['Week', 'Completed', 'Created'],
                  //   ['Apr 1-7', 100, 40],
                  //   ['Apr 7-14', 117, 46],
                  //   ['Apr 14-21', 66, 11],
                  //   ['Apr 21-28', 103, 54],
                  // ]
                }
                options={{
                // Material design options
                colors: ['#3dd19d','lightblue', '#ffab91'],
                bar: { groupWidth: "60px" },
                chart: {
                    title: 'Weekly Performance',
                    subtitle: 'Tasks completion metrics',
                },
                }}
                getChartWrapper={(w) => (wrapper.current = w)}
            />
            </Suspense>
        </TabPanel>
        <TabPanel value={props.tabOpenNum} index={2}>
          <Suspense fallback={<CircularProgress style={{ color: "#3dd19d" }} />}>
            <Chart
                width={'450px'}
                height={'320px'}
                chartType="Bar"
                loader={<CircularProgress style={{ color: "#3dd19d" }} />}
                data={
                  chartData.monthly
                  // [
                  //   ['Month', 'Completed', 'Created', 'Assigned'],
                  //   ['Jan', 100, 60, 80],
                  //   ['Feb', 60, 50, 60],
                  //   ['Mar', 70, 50, 60],
                  //   ['Apr', 80, 50, 60],
                  // ]
              }
                options={{
                // Material design options
                colors: ['#3dd19d','lightblue', '#ffab91'],
                bar: { groupWidth: "60px" },
                chart: {
                    title: 'Monthly Performance',
                    subtitle: 'Tasks completion metrics',
                },
                }}
                getChartWrapper={(w) => (wrapper.current = w)}
            />
            </Suspense>
        </TabPanel>
      </Paper>
    );
  }

  const TabPanel = (props) => {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
        className="flex items-center justify-end w-full transition-all flex-end"
      >
        {value === index && (
          <span className="flex items-center justify-end w-full py-4 transition-all flex-end">
            <span>{children}</span>
          </span>
        )}
      </div>
    );
  }

export default Charts
