import { InputBase } from '@material-ui/core';
import {Search as SearchIcon} from '@material-ui/icons';
import React, { useState, useEffect } from 'react'
import BootstrapTooltip from './BootstrapTooltip';

const BoardsSortControls = ({ todoLists, setTodoLists, initialTodoLists, setInitialTodoLists }) => {

    const [ascendingCompleted, setAscendingCompleted] = useState(false);
    const [ascendingCreated, setAscendingCreated] = useState(false);
    const [searchText, setSearchText] = useState('');

    const sortCreatedAscending = (a,b) => {
        let created1 = a.createdAt ? a.createdAt : new Date(1619421204*1000).getTime(); // April 26, 2021
        let created2 = b.createdAt ? b.createdAt : new Date(1619421204*1000).getTime();

        return (created2-created1);
    }
    const sortCreatedDescending = (a,b) => {
        let created1 = a.createdAt ? a.createdAt : new Date(1619421204*1000).getTime(); // April 26, 2021
        let created2 = b.createdAt ? b.createdAt : new Date(1619421204*1000).getTime();

        return (created1-created2);
    }

    useEffect(() => {
        let tempArr = [...todoLists];
        if(ascendingCreated){
            const sortedTodoLists = tempArr.sort((a,b)=>sortCreatedAscending(a,b));
            setTodoLists((prev)=>sortedTodoLists);
        }
        if(!ascendingCreated){
            const sortedTodoLists = tempArr.sort((a,b)=>sortCreatedDescending(a,b));
            setTodoLists((prev)=>sortedTodoLists);
        }
    }, [ascendingCreated])

    useEffect(() => {
        if(todoLists.length<1) return;
        const tempArr = [...todoLists];
        tempArr.sort((board1,board2)=>{
            let numOfTasksCompletedB1 = 0;
            let totalTasksB1 = board1.todoList.length;
            let numOfTasksCompletedB2 = 0;
            let totalTasksB2 = board2.todoList.length;
            if(totalTasksB1>0){
                board1.todoList.map(taskObj=>{
                    if(taskObj.isComplete) numOfTasksCompletedB1= numOfTasksCompletedB1 + 1;
                })
            }
            if(totalTasksB2>0){
                board2.todoList.map(taskObj=>{
                    if(taskObj.isComplete) numOfTasksCompletedB2= numOfTasksCompletedB2 + 1;
                })
            }
            const completedPercentB1 = totalTasksB1 && numOfTasksCompletedB1 ? Math.round(( numOfTasksCompletedB1 * 100 ) / totalTasksB1) : 0;
            const completedPercentB2 = totalTasksB2 && numOfTasksCompletedB2 ? Math.round(( numOfTasksCompletedB2 * 100 ) / totalTasksB2) : 0;
            if(ascendingCompleted){
                return completedPercentB1-completedPercentB2;
            } else {
                return completedPercentB2-completedPercentB1;
            }
        })
        setTodoLists(tempArr);
    }, [ascendingCompleted])

    const handleSearchChange = (e) => {
        setSearchText(e.target.value);
        const value = e.target.value.toLowerCase();
        const tempArr = [...initialTodoLists];
        const tempFilteredArr = tempArr.filter(boardObj=>
            boardObj?.name?.toLowerCase().includes(value) 
            || boardObj?.description?.toLowerCase().includes(value) 
            || boardObj?.createdBy?.toLowerCase().includes(value)
            || boardObj?.createdByUsername?.toLowerCase().includes(value)
        );
        // console.log('tempFilteredArr',tempFilteredArr);
        setTodoLists(tempFilteredArr);
    }

    useEffect(() => {
        setTodoLists(initialTodoLists);
    }, [])

    return (
        <div className="flex flex-wrap items-center justify-start w-full px-3 space-x-3 space-y-2 md:px-0">
            <span className="w-40 ml-4 mr-2 space-x-3 row-center">
                <span className="space-x-2 row-center">
                    <SearchIcon style={{color:"#3dd19d"}}/>
                    <InputBase
                        placeholder="Search boards…"
                        onChange={(e) => handleSearchChange(e)}
                        value={searchText}
                        inputProps={{'aria-label': 'search'}}
                    />
                </span>
            </span>
            <span className="space-x-1 md:space-x-3 row-center">
                {/* <span className="text-lg font-medium text-gray-700">Sort by Completion Percentage:</span> */}
                <BootstrapTooltip placement="top" title="Sort by completion percentage">
                    <span className="flex-wrap space-x-2 row-center">
                        <button onClick={()=>setAscendingCompleted(false)} 
                            // className={`px-3 py-1 my-1 font-medium text-white transition-all ${!ascendingCompleted ? "bg-green" : "bg-gray-400"} rounded-lg shadow-md hover:shadow-lg pointer`}
                            className={`px-3 py-1 my-1 font-medium text-white transition-all ${!ascendingCompleted ? "bg-green" : "bg-gray-400"} rounded-lg shadow-md hover:shadow-lg pointer`}
                        >
                            Most Progress
                        </button>
                        <button onClick={()=>setAscendingCompleted(true)} className={`px-3 py-1 font-medium text-white transition-all rounded-lg shadow-md ${ascendingCompleted ? "bg-green" : "bg-gray-400"} hover:shadow-lg pointer`}>
                            Least Progress
                        </button>
                    </span>
                </BootstrapTooltip>
            </span>
            <span className="space-x-1 md:space-x-3 row-center">
                {/* <span className="text-lg font-medium text-gray-700">Sort by Create Date:</span> */}
                <BootstrapTooltip placement="top" title="Sort by Create Date">
                    <span className="flex-wrap space-x-2 row-center">
                        <button onClick={()=>setAscendingCreated(true)} className={`px-3 py-1 font-medium text-white transition-all rounded-lg shadow-md ${ascendingCreated ? "bg-green" : "bg-gray-400"} hover:shadow-lg pointer`}>
                            Most recent                            
                        </button>
                        <button onClick={()=>setAscendingCreated(false)} className={`px-3 py-1 my-1 font-medium text-white transition-all ${!ascendingCreated ? "bg-green" : "bg-gray-400"} rounded-lg shadow-md hover:shadow-lg pointer`}>
                            Oldest
                        </button>
                    </span>
                </BootstrapTooltip>
            </span>
        </div>
    )
}

export default BoardsSortControls
