import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
} from "@material-ui/core";


import { withStyles, makeStyles } from '@material-ui/core/styles';

// import OkayIcon from "@material-ui/icons/";

import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";

const useStyless = makeStyles((theme) => ({
    modal: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
    paper: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: theme.palette.background.paper,
      border: "2px solid #000",
      boxShadow: theme.shadows[5],
      padding: theme.spacing(4, 2, 2),
    },
  }));

const MobilePopup = ()=> {
    const classes = useStyless();

    const [ openMobilePopup, setOpenMobilePopup] = useState(true);
  
    const handleClose = () => {
      setOpenMobilePopup(false);
    };  
    
    return (
    <>
    {
      openMobilePopup ?
      <div>
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={openMobilePopup}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 5000,
          }}
        >
          <Fade in={openMobilePopup}>
            <div className={classes.paper}>
              <h4 id="transition-modal-title">
                Please use desktop/laptop. Mobile friendly Todue is still in development.
              </h4>
              <Box mt={2}>
                <Button
                  onClick={(event) => {
                        handleClose();
                      }}
                  style={{color:'white', background:'#3dd19d'}}
                  variant="contained"
                >
                {/* <OkayIcon style={{marginRight:'10px'}} /> */}
                    Okay
                </Button>
              </Box>
            </div>
          </Fade>
        </Modal>
      </div>
      :
      <></>
      }
    </>
    );
  }

  export default MobilePopup;