import dayjs from 'dayjs';
import isBetween from 'dayjs/plugin/isBetween';
import { dateMaker, dateMakerMonthly, filterArrOfObj, dateMakerWeekly } from './extraUtils';

dayjs.extend(isBetween);

const extractStats = (tasks, currentUserId) => {

  const checkAssignedToOthers = (task) => {
    if (
      task.assignedBy === currentUserId &&
      task.assignedTo !== "" &&
      task.assignedTo !== currentUserId
    ) return true;
    else return false;
  }

  const checkAssignedToMyself = (task) => {
    if (
      task.assignedBy === currentUserId &&
      task.assignedTo !== "" &&
      task.assignedTo === currentUserId
    ) return true;
    else return false;
  }

  const checkAssignee = (task) => {
    if (
      task.assignedTo === currentUserId 
      && task.assignedBy !== currentUserId
    ) return true;
    else return false;
  }

const tasksSortedByCreatedAt = tasks.sort((a,b)=>a.createDate-b.createDate);
const tasksSortedByCompletedAt = tasks.sort((a,b)=>a?.completedAt-b?.completedAt);
// const completedTasksSorted = tasks.filter(task=>task.isComplete).sort((a,b)=>a.completedAt-b.completedAt);

  const getWeekDay = (date) => {
    const dow = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
    const dayNum = new Date(date).getDay();
    const day = dow[dayNum];
    // `${day} ${date.split('-')[2]}`
    return day;
  }

  const getMonth = (date) => {
    const moy = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    const monNum = new Date(date).getMonth();
    const month = moy[monNum];
    // `${day} ${date.split('-')[2]}`
    return month;
  }

  const makeWeek = (date) => {
    const moy = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

    const month = Number(date?.split('-')[1])-1;
    const wom = Number(date?.split('-')[2]);

    const monthString = moy[month];

    return `Week ${wom}, ${monthString}`;
  }

  const extractDailyStats = (timeseriesObj) => {
    const keys = Object.keys(timeseriesObj);
    
    const stats = [['Date', 'Completed', 'Created', 'Assigned']];

    keys?.map((key, index)=>{
      if(index>5) return;
      const values = Object.values(timeseriesObj[key]);
      const arrToPush = [getWeekDay(key), ...values];
      stats.push(arrToPush);
    })

    // console.log(dayjs(dateMaker(1613205056048)).isSame(dateMaker(1613205056048), 'day'))
    console.log(stats)

    return stats;
  }
  
  const extractMonthlyStats = (timeseriesObj) => {
    const keys = Object.keys(timeseriesObj)?.sort();
    const stats = [['Month', 'Completed', 'Created', 'Assigned']];

    keys?.map((key, index)=>{
      if(index>6) return;
      const values = Object.values(timeseriesObj[key]);
      const arrToPush = [getMonth(key), ...values];
      stats.push(arrToPush);
    })

    console.log(stats)
    return stats;
  }

  const extractWeeklyStats = (timeseriesObj) => {
    const keysSorted = Object.keys(timeseriesObj)?.sort();
    const keys = keysSorted.slice(keysSorted.length-3)
      // (a.split('-')[0]-b.split('-')[0])
      // &&
      // (a.split('-')[1]-b.split('-')[1])
      // &&
      // (a.split('-')[2]-b.split('-')[2])
      // ).reverse();

    // const splitted = Object.keys(timeseriesObj)?.split('-');
    // const year

    console.log('keys',keys); 
    const stats = [['Week', 'Completed', 'Created', 'Assigned']];

    keys?.map((key, index)=>{
      // if(index>3) return;
      const values = Object.values(timeseriesObj[key]);
      const arrToPush = [makeWeek(key), ...values];
      stats.push(arrToPush);
    })

    console.log(stats)
    return stats;
  }

  const timeseriesObj = {};
  const timeseriesObjMonthly = {};
  const timeseriesObjWeekly = {};

  tasksSortedByCreatedAt.map(task=>{ 
    if(task?.completedAt && task.isComplete && (checkAssignee(task) || checkAssignedToMyself(task))) {
      const prev = timeseriesObj[dateMaker(task.completedAt)];
      const prevMonthly = timeseriesObjMonthly[dateMakerMonthly(task.completedAt)];
      const prevWeekly = timeseriesObjWeekly[dateMakerWeekly(task.completedAt)];
      if(prev){
        timeseriesObj[dateMaker(task.completedAt)] = {...prev, completed: prev.completed ? prev.completed + 1 : 1};
      } else {
        timeseriesObj[dateMaker(task.completedAt)] = {completed: 1, created: 0, assigned : 0};
      }
      if(prevMonthly){
        timeseriesObjMonthly[dateMakerMonthly(task.completedAt)] = {...prevMonthly, completed: prevMonthly.completed ? prevMonthly.completed + 1 : 1};
      } else {
        timeseriesObjMonthly[dateMakerMonthly(task.completedAt)] = {completed: 1, created: 0, assigned : 0};
      }
      if(prevWeekly){
        timeseriesObjWeekly[dateMakerWeekly(task.completedAt)] = {...prevWeekly, completed: prevWeekly.completed ? prevWeekly.completed + 1 : 1};
      } else {
        timeseriesObjWeekly[dateMakerWeekly(task.completedAt)] = {completed: 1, created: 0, assigned : 0};
      }
    }
    if(checkAssignedToOthers(task)) {
      const prev = timeseriesObj[dateMaker(task.createDate)];
      const prevMonthly = timeseriesObjMonthly[dateMakerMonthly(task.createDate)];
      const prevWeekly = timeseriesObjWeekly[dateMakerWeekly(task.createDate)];
      if(prev){
        timeseriesObj[dateMaker(task.createDate)] = {...prev, assigned: prev.assigned ? prev.assigned + 1 : 1};
      } else {
        timeseriesObj[dateMaker(task.createDate)] = {completed: 0, created: 0, assigned : 1};
      }
      if(prevMonthly){
        timeseriesObjMonthly[dateMakerMonthly(task.createDate)] = {...prevMonthly, assigned: prevMonthly.assigned ? prevMonthly.assigned + 1 : 1};
      } else {
        timeseriesObjMonthly[dateMakerMonthly(task.createDate)] = {completed: 0, created: 0, assigned : 1};
      }
      if(prevWeekly){
        timeseriesObjWeekly[dateMakerWeekly(task.createDate)] = {...prevWeekly, assigned: prevWeekly.assigned ? prevWeekly.assigned + 1 : 1};
      } else {
        timeseriesObjWeekly[dateMakerWeekly(task.createDate)] = {completed: 0, created: 0, assigned : 1};
      }
    }
    if(checkAssignedToMyself(task) || checkAssignedToOthers(task)) {
      const prev = timeseriesObj[dateMaker(task.createDate)];
      const prevMonthly = timeseriesObjMonthly[dateMakerMonthly(task.createDate)];
      const prevWeekly = timeseriesObjWeekly[dateMakerWeekly(task.createDate)];
      if(prev){
        timeseriesObj[dateMaker(task.createDate)] = {...prev, created: prev.created ? prev.created + 1 : 1};
      } else {
        timeseriesObj[dateMaker(task.createDate)] = {completed: 0, created: 1, assigned : 0};
      }
      if(prevMonthly){
        timeseriesObjMonthly[dateMakerMonthly(task.createDate)] = {...prevMonthly, created: prevMonthly.created ? prevMonthly.created + 1 : 1};
      } else {
        timeseriesObjMonthly[dateMakerMonthly(task.createDate)] = {completed: 0, created: 1, assigned : 0};        
      }
      if(prevWeekly){
        timeseriesObjWeekly[dateMakerWeekly(task.createDate)] = {...prevWeekly, created: prevWeekly.created ? prevWeekly.created + 1 : 1};
      } else {
        timeseriesObjWeekly[dateMakerWeekly(task.createDate)] = {completed: 0, created: 1, assigned : 0};        
      }
    }
    })
    console.log('timeseriesObjMonthly', timeseriesObjMonthly)
    return {
      daily: extractDailyStats(timeseriesObj),
      monthly: extractMonthlyStats(timeseriesObjMonthly),
      weekly: extractWeeklyStats(timeseriesObjWeekly)
    } 
};

const isNotEmpty = (toCheck) => toCheck?.length;


export const statsMaker = (tasksData, currentUserId) => {

  if(isNotEmpty(tasksData)){
    const allTasksRaw = tasksData.map(boardObj=>[...boardObj.todoList]).flat();
    const allTasks = filterArrOfObj(allTasksRaw, "taskId");
    
    if(isNotEmpty(allTasks)){
      console.log('extractStats',extractStats(allTasks, currentUserId))
      return extractStats(allTasks, currentUserId);
    }
  }
    
  return null;
}